.section-login {
    @extend .site-bg;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    box-sizing: border-box;
    @include susy-breakpoint($s-320, $c8) {
        padding: 0 4rem;
    }
    &__block {
        @extend .site-transparent-block;
        width: 76rem;
        height: 49rem;
        box-sizing: border-box;
        padding: 5rem;
        position: relative;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            height: 48rem;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;
            padding-bottom: 4rem;
            padding-left: 2rem;
            padding-right: 2rem;
        }
    }
    &__firstins-logo {
        width: 22.8rem;
        height: 11.4rem;
        background: transparent url('../images/firstins-logo.png') no-repeat 0 0;
        background-size: cover;
        font-size: 0;
        @include susy-breakpoint($s-320, $c8) {
            width: 17rem;
            height: 8.6rem;
            margin: 0 auto;
        }
    }
    &__form {
        width: 27rem;
        position: absolute;
        right: 10rem;
        bottom: 7rem;
        z-index: 100;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            position: relative;
            right: inherit;
            bottom: inherit;
        }
    }
    &__rows {
        position: relative;
        &:not(:last-child) {
            margin-bottom: 1.6rem;
        }
    }
    &__input {
        box-sizing: border-box;
        display: block;
        width: 100%;
        font-weight: 500;
        border: 0;
        padding: 1.2rem 1.6rem;
        background-color: #fff;
        border-radius: 6rem;
        font-size: var(--font-size-14);
        color: #333;
        transition: all 300ms ease;
        &::placeholder {
            color: #909090;
        }
        &:focus {
            background-color: var(--grey-color-ee);
        }
        &.password {
            padding-right: 3rem;
        }
    }
    &__btn-toggle-psw {
        cursor: pointer;
        width: 3rem;
        height: 3rem;
        position: absolute;
        top: 50%;
        right: 0.8rem;
        transform: translate3d(0,-50%,0);
        border: 0;
        background-color: transparent;
        display: flex;
        justify-content: center;
        align-items: center;
        i {
            color: #cacaca;
            font-size: 3rem;
            transition: all 300ms ease;
            pointer-events: none;
        }
        &:hover {
            i {
                color: #4e4d4d;
            }
        }
        &:active {
            i {
                color: #d76270;
            }
        }
        &.active {
            i {
                color: #d76270;
            }
        }
    }
    &__btn-login {
        box-sizing: border-box;
        width: 100%;
        display: block;
        background-color: var(--main-color-500);
        border-radius: 6rem;
        color: #fff;
        font-size: var(--font-size-14);
        padding: 1.2rem 1.6rem;
        border: 0;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--main-color-700);
        }
    }
    &__message {
        font-size: var(--font-size-14);
        color: #000;
        line-height: 1.6;
        text-align: center;
    }
    &__clouds-left {
        width: 50.7rem;
        height: 27.4rem;
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 50;
        background: transparent url('../images/clouds.png') no-repeat 0 0;
        background-size: cover;
        transform: translate3d(-148%,0%,0);
        pointer-events: none;
        animation-name: login-cloud-move-left;
        animation-timing-function: linear;
        animation-duration: 20s;
        animation-fill-mode: both;
        animation-direction: alternate;
        animation-iteration-count: infinite;
        @include susy-breakpoint($s-320, $c8) {
            transform: translate3d(-108%,70%,0);
        }
    }
    &__clouds-right {
        width: 61.6rem;
        height: 33.3rem;
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 50;
        background: transparent url('../images/clouds.png') no-repeat 0 0;
        background-size: cover;
        transform: translate3d(15%,-118%,0) scaleX(-1);
        pointer-events: none;
        opacity: 0.3;
        animation-name: login-cloud-move-right;
        animation-timing-function: linear;
        animation-duration: 20s;
        animation-fill-mode: both;
        animation-direction: alternate;
        animation-iteration-count: infinite;
        @include susy-breakpoint($s-320, $c8) {
            transform: translate3d(-25%,-58%,0) scaleX(-1);
        }
    }
}

@keyframes login-cloud-move-left {
    0% {
        transform: translate3d(-148%,0%,0);
    }
    100% {
        transform: translate3d(-138%,0%,0);
    }
}

@keyframes login-cloud-move-right {
    0% {
        transform: translate3d(15%,-118%,0) scaleX(-1);
    }
    100% {
        transform: translate3d(0,-118%,0) scaleX(-1);
    }
}
