.meeting-room-page {
    padding: 0 2.4rem;
    position: relative;
    min-height: 100%;
    @include susy-breakpoint($s-320, $c8) {
        padding: 0;
    }
    @include susy-breakpoint($s-768, $c12) {
        padding: 0;
    }
    @include susy-breakpoint($s-960, $c12) {
        padding: 0;
    }
    &__uc {
        min-height: 60rem;
        display: flex;
        align-items: center;
        justify-content: center;
        @include susy-breakpoint($s-320, $c8) {
            min-height: 40rem;
        }
    }
    &__uc-msg {
        img {
            width: 33rem;
            display: block;
            margin: 0 auto 2rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 23rem;
            }
        }
        p {
            text-align: center;
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
        }
    }
}

.meeting-room-nav {
    position: absolute;
    background-color: var(--grey-color-dd);
    border-radius: 0.8rem;
    overflow: hidden;
    display: flex;
    padding: 0.8rem;
    gap: 0.8rem;
    box-sizing: border-box;
    right: 2.4rem;
    top: 3rem;
    z-index: 10;
    @include susy-breakpoint($s-320, $c8) {
        position: relative;
        right: inherit;
        top: inherit;
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        background-color: var(--grey-color-c9);
        border-radius: 0 0 0.8rem 0.8rem;
        padding: 1.6rem;
        margin-bottom: 2rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        top: 2rem;
        right: 2rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        top: 2rem;
        right: 2rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        top: 2rem;
    }
    &__btn-link {
        box-sizing: border-box;
        font-size: var(--font-size-14);
        color: var(--grey-color-52);
        padding: 1rem 1.6rem;
        background-color: #fff;
        border-radius: 0.4rem;
        white-space: nowrap;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--grey-color-ba);
        }
        &.active {
            background-color: var(--sub-color-400);
            color: #fff;
            &:hover {
                background-color: var(--main-color-400);
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            flex-basis: 50%;
            text-align: center;
            white-space: normal;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding: 1rem 0.8rem;
        }
    }
}

.meeting-room-area-tab {
    display: none;
    gap: 0.8rem;
    overflow: hidden;
    @include susy-breakpoint($s-320, $c8) {
        display: flex;
    }
    &__btn-tab {
        box-sizing: border-box;
        width: 100%;
        background-color: var(--grey-color-bd);
        font-size: var(--font-size-20);
        padding: 0.8rem 1.2rem;
        border-radius: 0.8rem 0.8rem 0 0;
        text-align: left;
        border: 0;
        color: #fff;
        transition: all 300ms ease;
        transform: translate3d(0,0.4rem,0);
        &.active {
            transform: translate3d(0,0,0);
            background-color: #fff;
            color: var(--grey-color-52);
        }
    }
}

.meeting-room-area-wrap {
    @include susy-breakpoint($s-320, $c8) {
        background-color: #fff;
        padding: 2rem;
        border-radius: 0 0 0.8rem 0.8rem;
    }
}

.meeting-room-area {
    $self: &;
    margin-bottom: 4.8rem;
    @include susy-breakpoint($s-320, $c8) {
        display: none;
        margin-bottom: 0;
    }
    @include susy-breakpoint($s-768, $c12) {
        margin-bottom: 2rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        margin-bottom: 3.2rem;
    }
    &.headquarters {
        #{$self}__info {
            background-color: var(--sub-color-100);
        }
    }
    &.yanping {
        #{$self}__info {
            background-color: var(--blue-color-100);
        }
    }
    &__header {
        display: flex;
        align-items: center;
        height: 4.6rem;
        margin-bottom: 1.6rem;
        @include susy-breakpoint($s-320, $c8) {
            display: none;
        }
    }
    &__title {
        @extend %page-title;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: min-content;
        gap: 2.4rem 4.8rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: repeat(2, 1fr);
            gap: 1.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 1.6rem 2.4rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            gap: 1.6rem 2.4rem;
        }
    }
    &__item {
        overflow: hidden;
        display: flex;
        flex-direction: column;
        height: 100%;
    }
    &__img {
        border-radius: 0.8rem 0.8rem 0 0;
        padding: 1.2rem 2rem;
        background-color: var(--grey-color-ec);
        img {
            max-width: 100%;
            display: block;
            margin-bottom: 0;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding: 1rem 1.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding: 1rem 1.6rem;
        }
    }
    &__info {
        flex: 1;
        padding: 1.2rem 2rem;
        display: grid;
        grid-template-columns: 1fr 8rem;
        gap: 0.8rem;
        border-radius: 0 0 0.8rem 0.8rem;
        @include susy-breakpoint($s-768, $c12) {
            padding: 1rem 1.6rem;
            grid-template-columns: 1fr 6.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding: 1rem 1.6rem;
            grid-template-columns: 1fr 6.6rem;
        }
    }
    &__detail {
        h4 {
            font-weight: 500;
            font-size: var(--font-size-18);
            color: var(--grey-color-52);
            line-height: 1.6;
        }
        p {
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
        }
    }
    &__btn-booking {
        margin-top: 0.4rem;
        width: 8rem;
        height: 4.4rem;
        background-color: var(--grey-color-7e);
        font-size: var(--font-size-14);
        color: #fff;
        display: block;
        border-radius: 0.4rem;
        line-height: 4.4rem;
        text-align: center;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--grey-color-52);
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 6.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 6.6rem;
        }
    }
}

.meeting-room-overview {
    $self: &;
    @include susy-breakpoint($s-320, $c8) {
        background-color: #fff;
        padding: 2rem;
        border-radius: 0.8rem;
        margin-bottom: 8rem;
    }
    &__header {
        display: flex;
        align-items: center;
        height: 4.6rem;
        margin-bottom: 1.6rem;
        @include susy-breakpoint($s-320, $c8) {
            flex-wrap: wrap;
            height: auto;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding-top: 6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            margin-bottom: 2.4rem;
        }
    }
    &__title {
        @extend %page-title;
        margin-right: 15vw;
        @include susy-breakpoint($s-320, $c8) {
            margin-right: 0;
            margin-bottom: 2rem;
            margin-left: 0;
        }
        @include susy-breakpoint($s-960, $c12) {
            margin-right: 0.8rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            margin-right: 6rem;
        }
    }
    &__datepicker-wrap {
        display: flex;
        gap: 1.6rem;
        align-items: center;
        @include susy-breakpoint($s-320, $c8) {
            flex-basis: 100%;
            justify-content: center;
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 0.8rem;
        }
    }
    &__btn-prev,
    &__btn-next {
        width: 4.6rem;
        height: 4rem;
        position: relative;
        background-color: var(--grey-color-dd);
        border-radius: 0.8rem;
        border: 0;
        display: block;
        transition: all 300ms ease;
        i {
            font-size: var(--font-size-18);
            color: var(--main-color-500);
        }
        &:hover {
            background-color: var(--grey-color-ba);
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 3.2rem;
            height: 3.2rem;
            i {
                font-size: var(--font-size-16);
            }
        }
    }
    &__input {
        text-align: center;
        padding: 0;
        border: 0;
        height: 4rem;
        border-radius: 0.8rem;
        width: 13rem;
        background-color: var(--grey-color-dd);
        font-size: var(--font-size-18);
        color: var(--grey-color-3f);
        @include susy-breakpoint($s-960, $c12) {
            height: 3.2rem;
            width: 10rem;
            font-size: var(--font-size-16);
        }
    }
    &__date-wrap {
        display: grid;
        grid-template-columns: repeat(5, minmax(0, 1fr));
        gap: 2.4rem;
        margin-bottom: 8.8rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: minmax(0, 1fr);
            padding: 0 2rem;
            margin-bottom: 2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: repeat(2, minmax(0, 1fr));
            gap: 1.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            grid-template-columns: repeat(3, minmax(0, 1fr));
        }
        @include susy-breakpoint($s-1201, $c18) {
            grid-template-columns: repeat(3, minmax(0, 1fr));
        }
    }
    &__date-item {
        & > h4 {
            height: 4rem;
            line-height: 4rem;
            text-align: center;
            background-color: var(--grey-color-dd);
            font-size: var(--font-size-18);
            color: #000;
            font-weight: normal;
            border-radius: 0.8rem 0.8rem 0 0;
        }
    }
    &__room-list {
        border: solid 1px var(--grey-color-d5);
        box-sizing: border-box;
        width: 100%;
        padding: 0.8rem;
        display: flex;
        flex-direction: column;
        gap: 0.8rem;
        border-radius: 0 0 0.8rem 0.8rem;
    }
    &__room {
        position: relative;
        z-index: 0;
        &.active {
            z-index: 10;
            #{$self}__details-wrap {
                opacity: 1;
                pointer-events: auto;
            }
        }
        & > h5 {
            color: var(--grey-color-3f);
            font-size: var(--font-size-16);
            font-weight: normal;
            padding: 0.8rem;
            border-radius: 0.4rem;
            position: relative;
            cursor: pointer;
            &::after {
                width: 1.8rem;
                height: 1.8rem;
                position: absolute;
                content: "";
                top: 50%;
                background: transparent url('../images/icon-info.svg') no-repeat 0 0;
                background-size: 1.8rem;
                right: 0.8rem;
                transform: translate3d(0,-50%,0);
            }
        }
        &.headquarters {
            & > h5 {
                background-color: var(--sub-color-100);
            }
        }
        &.yanping {
            & > h5 {
                background-color: var(--blue-color-100);
            }
        }
    }
    &__details-wrap {
        width: 100%;
        position: absolute;
        top: 3.2rem;
        left: 0;
        background-color: var(--grey-color-2d);
        border-radius: 0.4rem;
        padding: 1.2rem;
        box-sizing: border-box;
        pointer-events: none;
        opacity: 0;
        & > h5 {
            font-size: var(--font-size-16);
            color: #fff;
            margin-bottom: 0.8rem;
            font-weight: normal;
        }
    }
    &__btn-close {
        width: 2rem;
        height: 2rem;
        border: 0;
        background-color: transparent;
        position: absolute;
        right: 1.6rem;
        top: 1.6rem;
        padding: 0;
        i {
            pointer-events: none;
            color: var(--grey-color-62);
            font-size: var(--font-size-16);
            transition: all 300ms ease;
        }
        &:hover {
            i {
                color: var(--grey-color-92);
            }
        }
    }
    &__details-list {
        display: flex;
        flex-direction: column;
        gap: 0.4rem;
    }
    &__details {
        span {
            font-size: var(--font-size-14);
            color: #fff;
            line-height: 1.6;
            &:nth-child(1) {
                display: block;
            }
        }
    }
    &__footer {
        position: absolute;
        right: 2.4rem;
        bottom: 0;
        display: flex;
        justify-content: flex-end;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            right: 0;
        }
    }
    &__footer-inner {
        align-items: center;
        gap: 1.6rem;
        display: flex;
        padding: 1.2rem 1.6rem;
        border-radius: 0.8rem;
        background-color: var(--grey-color-dd);
        h3 {
            font-weight: normal;
            font-size: var(--font-size-20);
            border-left: solid 0.4rem var(--main-color-500);
            padding-left: 0.4rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-16);
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            justify-content: center;
        }
    }
    &__location {
        display: flex;
        gap: 1.2rem;
        align-items: center;
        span {
            font-size: var(--font-size-16);
            color: #000;
            position: relative;
            line-height: 2.2rem;
            padding-left: 2.8rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-14);
            }
            &::before {
                content: "";
                position: absolute;
                left: 0;
                top: 0;
                width: 2rem;
                height: 2rem;
                border: solid 0.1rem var(--grey-color-76);
                border-radius: 50%;
            }
            &:nth-child(1) {
                &::before {
                    background-color: var(--sub-color-100);
                }
            }
            &:nth-child(2) {
                &::before {
                    background-color: var(--blue-color-100);
                }
            }
        }
    }
}

.meeting-room-search {
    $self: &;
    @include susy-breakpoint($s-320, $c8) {
        background-color: #fff;
        padding: 2rem;
        border-radius: 0.8rem;
        margin-bottom: 20rem;
    }
    &__header {
        display: flex;
        align-items: center;
        height: 4.6rem;
        margin-bottom: 1.6rem;
    }
    &__title {
        @extend %page-title;
        @include susy-breakpoint($s-320, $c8) {
            margin-left: 0;
        }
    }
    &__wrap {
        display: grid;
        grid-template-columns: minmax(0, 2fr) minmax(0, 1fr);
        gap: 5.6rem;
        padding: 4rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: minmax(0, 1fr);
            grid-template-rows: repeat(2, min-content);
            gap: 2rem;
            padding: 0;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: minmax(0, 1fr);
            grid-template-rows: repeat(2, min-content);
            padding: 1.6rem;
            gap: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding: 0;
            gap: 2rem;
            grid-template-columns: minmax(0, 1fr) 16rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            gap: 3.2rem;
            padding: 2rem;
            grid-template-columns: minmax(0, 1fr) 16rem;
        }
    }
    &__search-form {
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 4.8rem 7.2rem;
        @include susy-breakpoint($s-320, $c8) {
            gap: 1.6rem;
            grid-template-columns: minmax(0, 1fr);
            grid-template-rows: repeat(3, min-content);
        }
        @include susy-breakpoint($s-768, $c12) {
            gap: 1.6rem;
            grid-template-columns: minmax(0, 1fr);
            grid-template-rows: repeat(3, min-content);
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 2rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            gap: 2.4rem;
        }
    }
    &__time {
        grid-column-start: 1;
        grid-column-end: 2;
        grid-row-start: 1;
        grid-row-end: 2;
        display: flex;
        flex-direction: column;
    }
    &__place {
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 2;
        display: flex;
        flex-direction: column;
        @include susy-breakpoint($s-320, $c8) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 2;
            grid-row-end: 3;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 2;
            grid-row-end: 3;
        }
    }
    &__submit {
        grid-column-start: 1;
        grid-column-end: 3;
        grid-row-start: 2;
        grid-row-end: 3;
        @include susy-breakpoint($s-320, $c8) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 3;
            grid-row-end: 4;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 3;
            grid-row-end: 4;
        }
    }
    &__search-header {
        background-color: var(--blue-color-500);
        display: flex;
        padding: 4rem;
        align-items: center;
        border-radius: 0.8rem 0.8rem 0 0;
        &.time {
            background-color: var(--orange-color-500);
        }
        &.place {
            background-color: var(--blue-color-500);
        }
        @include susy-breakpoint($s-768, $c12) {
            justify-content: center;
        }
    }
    &__icon {
        width: 7.2rem;
        height: 7.2rem;
        img {
            max-width: 100%;
            display: block;
            margin: 0;
        }
    }
    &__info {
        h3 {
            font-size: var(--font-size-56);
            font-weight: normal;
            color: #fff;
            margin-bottom: 0.8rem;
        }
        h4 {
            font-size: var(--font-size-14);
            color: #fff;
            font-weight: normal;
        }
    }
    &__body {
        background-color: var(--grey-color-dd);
        padding: 3.2rem 2.4rem;
        border-radius: 0 0 0.8rem 0.8rem;
        flex: 1;
        @include susy-breakpoint($s-960, $c12) {
            padding: 1.6rem;
        }
    }
    &__row {
        display: flex;
        align-items: center;
        gap: 1.6rem;
        &:not(:last-child) {
            margin-bottom: 1.6rem;
        }
    }
    &__label {
        font-size: var(--font-size-16);
        color: var(--grey-color-52);
        white-space: nowrap;
        @include susy-breakpoint($s-960, $c12) {
            font-size: var(--font-size-14);
        }
    }
    &__input {
        width: 100%;
        padding: 0;
        border: 0;
        height: 4.8rem;
        line-height: 4.8rem;
        border-radius: 0.8rem 0 0 0.8rem;
        padding: 0 0.8rem;
        box-sizing: border-box;
        background-color: #fff;
        font-size: var(--font-size-16);
        color: var(--grey-color-69);
        flex-shrink: 1;
        @include susy-breakpoint($s-960, $c12) {
            font-size: var(--font-size-14);
        }
    }
    &__select-wrap {
        @extend %site-select-search;
        @include susy-breakpoint($s-320, $c8) {
            select {
                background-color: #fff;
                padding-right: 6.6rem;
            }
        }
        @include susy-breakpoint($s-960, $c12) {
            select {
                font-size: var(--font-size-14);
            }
        }
    }
    &__datepicker,
    &__timepicker {
        display: grid;
        grid-template-columns: 1fr 6.6rem;
        overflow: hidden;
        @include susy-breakpoint($s-960, $c12) {
            grid-template-columns: 1fr 5.6rem;
        }
    }
    &__btn-pick {
        width: 6.6rem;
        height: 4.8rem;
        background-color: var(--grey-color-af);
        position: relative;
        border-radius: 0 0.8rem 0.8rem 0;
        cursor: pointer;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--grey-color-92);
        }
        &::after {
            position: absolute;
            content: "\e915";
            font-family: "one";
            font-size: var(--font-size-20);
            color: #fff;
            top: 50%;
            left: 50%;
            
            transform: translate3d(-50%,-50%,0) rotate(90deg);
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 5.6rem;
        }
    }
    &__btn-submit {
        display: block;
        width: 25rem;
        height: 4.8rem;
        border-radius: 4.8rem;
        background-color: var(--main-color-300);
        font-size: var(--font-size-16);
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0;
        margin: 0 auto 1.2rem;
        &:disabled {
            background-color: var(--grey-color-db);
        }
    }
    &__msg {
        text-align: center;
        font-size: var(--font-size-16);
        color: var(--grey-color-52);
