        border-radius: 0.8rem;
        padding: 0 0.8rem;
        box-sizing: border-box;
        background: var(--grey-color-dd);
        font-size: var(--font-size-16);
        color: var(--grey-color-69);
        @include susy-breakpoint($s-320, $c8) {
            height: 16rem;
            font-size: var(--font-size-14);
        }
    }
    &__select-wrap {
        @extend %site-select-search;
        select {
            background: var(--grey-color-dd);
        }
    }
    &__datepicker,
    &__timepicker {
        display: grid;
        grid-template-columns: 1fr 6.6rem;
        overflow: hidden;
        #{$self}__input {
            border-radius: 0.8rem 0 0 0.8rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            grid-template-columns: 1fr 5.6rem;
        }
    }
    &__btn-pick {
        width: 6.6rem;
        height: 4.8rem;
        background-color: var(--grey-color-af);
        position: relative;
        border-radius: 0 0.8rem 0.8rem 0;
        cursor: pointer;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--grey-color-92);
        }
        &::after {
            position: absolute;
            content: "\e915";
            font-family: "one";
            font-size: var(--font-size-20);
            color: #fff;
            top: 50%;
            left: 50%;
            transform: translate3d(-50%,-50%,0) rotate(90deg);
        }
        @include susy-breakpoint($s-320, $c8) {
            height: 4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 5.6rem;
        }
    }
    &__submit {
        flex-basis: 100%;
        display: flex;
        gap: 4rem;
        justify-content: center;
        margin-bottom: 6rem;
        @include susy-breakpoint($s-320, $c8) {
            gap: 2rem;
            margin-bottom: 2.4rem;
        }
    }
    &__btn-submit {
        width: 14rem;
        height: 4.8rem;
        border-radius: 4.8rem;
        background-color: var(--main-color-300);
        font-size: var(--font-size-16);
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--main-color-500);
        }
        &:disabled {
            background-color: var(--grey-color-db);
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 12rem;
            font-size: var(--font-size-14);
        }
    }
    &__btn-reset {
        width: 14rem;
        height: 4.8rem;
        border-radius: 4.8rem;
        background-color: var(--grey-color-62);
        font-size: var(--font-size-16);
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--grey-color-52);
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 12rem;
            font-size: var(--font-size-14);
        }
    }
    &__input-wrap {
        width: 100%;
    }
    &__date-wrap {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        grid-template-rows: repeat(2, minmax(0, 1fr));
        gap: 1.6rem;
        #{$self}__select-wrap {
            margin-bottom: 0;
            grid-column-start: 1;
            grid-column-end: 3;
            grid-row-start: 1;
            grid-row-end: 2;
            @include susy-breakpoint($s-320, $c8) {
                grid-column-start: 1;
                grid-column-end: 2;
                grid-row-start: 1;
                grid-row-end: 2;
            }
        }
        #{$self}__input-wrap {
            &.full {
                margin-bottom: 0;
                grid-column-start: 1;
                grid-column-end: 3;
                grid-row-start: 1;
                grid-row-end: 2;
                @include susy-breakpoint($s-320, $c8) {
                    grid-column-start: 1;
                    grid-column-end: 2;
                    grid-row-start: 1;
                    grid-row-end: 2;
                }
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: minmax(0, 1fr);
            grid-template-rows: repeat(3, minmax(0, 1fr));
        }
    }
}

.meeting-room-history {
    $self: &;
    @include susy-breakpoint($s-320, $c8) {
        background-color: #fff;
        border-radius: 0.8rem;
        padding-top: 2rem;
    }
    &__header {
        display: flex;
        align-items: center;
        height: 4.6rem;
        margin-bottom: 1.6rem;
    }
    &__title {
        @extend %page-title;
    }
    &__btn-cancel {
        padding: 0;
        border: 0;
        line-height: 4rem;
        height: 4rem;
        width: 11rem;
        border-radius: 0.4rem;
        font-size: var(--font-size-14);
        color: #fff;
        background-color: var(--sub-color-400);
        transition: all 300ms ease;
        &:hover {
            background-color: var(--main-color-500);
        }
        &:disabled {
            background-color: var(--grey-color-c7);
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 4.8rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 6.4rem;
        }
    }
    &__table {
        width: 100%;
        border-radius: 1rem;
        overflow: hidden;
        @include susy-breakpoint($s-320, $c8) {
            border-radius: 0;
        }
        tr {
            transition: all 300ms ease;
            &:nth-child(odd) {
                background-color: var(--grey-color-ee);
            }
            &:nth-child(even) {
                background-color: var(--grey-color-f9);
            }
            &:hover {
                background-color: var(--grey-color-dd);
            }
        }
        th {
            font-weight: normal;
            background-color: var(--grey-color-ba);
            font-size: var(--font-size-14);
            color: #fff;
            line-height: 1.6;
            padding: 1.8rem 4rem;
            vertical-align: middle;
            white-space: nowrap;
            &:nth-child(1) {
                text-align: left;
            }
            @include susy-breakpoint($s-320, $c8) {
                background-color: var(--grey-color-9c);
                padding: 1rem 0.8rem;
                &:nth-child(4),
                &:nth-child(5) {
                    display: none;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                padding: 1.2rem;
                &:nth-child(4),
                &:nth-child(5) {
                    display: none;
                }
                &:last-child {
                    width: 10rem;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                padding: 1.2rem 2rem;
                &:nth-child(4),
                &:nth-child(5) {
                    display: none;
                }
                &:last-child {
                    width: 10rem;
                }
            }
            @include susy-breakpoint($s-1201, $c18) {
                padding: 1.2rem 2rem;
                &:last-child {
                    width: 10rem;
                }
            }
        }
        td {
            text-align: center;
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
            vertical-align: middle;
            padding: 1.8rem 4rem;
            &:nth-of-type(1) {
                text-align: left;
                max-width: 16rem;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            @include susy-breakpoint($s-320, $c8) {
                padding: 1rem 0.8rem;
                &:nth-child(5),
                &:nth-child(6) {
                    display: none;
                }
                &:nth-of-type(1) {
                    max-width: 10rem;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                padding: 1.2rem;
                &:nth-child(5),
                &:nth-child(6) {
                    display: none;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                padding: 1.2rem 2rem;
                &:nth-child(5),
                &:nth-child(6) {
                    display: none;
                }
            }
            @include susy-breakpoint($s-1201, $c18) {
                padding: 1.2rem 2rem;
                &:nth-child(1) {
                    max-width: 15rem;
                }
            }
        }
    }
}

.remind-text-slide {
    width: 100%;
    overflow: hidden;
}