.user-profile {
    $self: &;
    position: absolute;
    top: 3rem;
    left: 50%;
    transform: translate3d(1480%,0,0);
    z-index: 200;
    @include susy-breakpoint($s-320, $c8) {
        transform: translate3d(-50%,0,0);
        display: flex;
        transform: translate3d(-50%,0,0);
        transition: all 300ms ease;
        opacity: 0;
        pointer-events: none;
        &.active {
            pointer-events: auto;
            opacity: 1;
            #{$self}__dropdown {
                pointer-events: auto;
            }
        }
    }
    @include susy-breakpoint($s-768, $c12) {
        left: inherit;
        right: 2rem;
        transform: translate3d(0,0,0);
    }
    @include susy-breakpoint($s-960, $c12) {
        left: inherit;
        right: 2rem;
        transform: translate3d(0,0,0);
    }
    @include susy-breakpoint($s-1201, $c18) {
        left: inherit;
        right: 2rem;
        transform: translate3d(0,0,0);
    }
    &__avatar {
        width: 4.5rem;
        height: 4.5rem;
        border-radius: 50%;
        border: solid 0.2rem #fff;
        overflow: hidden;
        cursor: pointer;
        img {
            @extend .rsp-img;
            pointer-events: none;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 9rem;
            height: 9rem;
        }
    }
    &__dropdown {
        width: 11rem;
        position: absolute;
        background-color: var(--grey-color-48);
        border-radius: 1rem;
        padding: 1.6rem 2.4rem;
        left: 50%;
        top: 6rem;
        transform: translate3d(-50%,-20%,0);
        opacity: 0;
        pointer-events: none;
        transition: all 300ms ease;
        &.active {
            transform: translate3d(-50%,0,0);
            opacity: 1;
            pointer-events: auto;
        }
        @include susy-breakpoint($s-320, $c8) {
            background-color: transparent;
            width: auto;
            opacity: 1;
            position: relative;
            left: 0;
            top: 0;
            transform: translate3d(0,0,0);
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            padding: 0.8rem 2rem;
            &.active {
                transform: translate3d(0,0,0);
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            transform: translate3d(-84%,-20%,0);
            &.active {
                transform: translate3d(-84%,0,0);
            }
        }
        @include susy-breakpoint($s-960, $c12) {
            transform: translate3d(-84%,-20%,0);
            &.active {
                transform: translate3d(-84%,0,0);
            }
        }
        @include susy-breakpoint($s-1201, $c18) {
            transform: translate3d(-84%,-20%,0);
            &.active {
                transform: translate3d(-84%,0,0);
            }
        }
        @include susy-breakpoint($s-1601, $c24) {
            transform: translate3d(-84%,-20%,0);
            &.active {
                transform: translate3d(-84%,0,0);
            }
        }
    }
    &__btn-greeting,
    &__btn-logout {
        color: #fff;
        transition: all 300ms ease;
        font-size: var(--font-size-14);
        display: block;
        white-space: nowrap;
        &:hover {
            color: #ffaab9;
        }
        &:not(:last-child) {
            margin-bottom: 2rem;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-20);
            &:not(:last-child) {
                margin-bottom: 1rem;
            }
            &:hover {
                color: #fff;
            }
        }
    }
}