.address-book-page {
    &__wrap {
        display: grid;
        grid-template-columns: 27rem 1fr;
        gap: 4rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
            gap: 2rem;
            select {
                background-color: var(--grey-color-e2);
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            gap: 2rem;
            grid-template-columns: 1fr;
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 2rem;
            grid-template-columns: 22rem 1fr;
        }
        @include susy-breakpoint($s-1201, $c18) {
            gap: 2rem;
            grid-template-columns: 24rem 1fr;
        }
    }
    &__filter-wrap {
        background-color: var(--grey-color-dd);
        border-radius: 1rem;
        padding: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            border-radius: 0 0 1rem 1rem;
            background-color: #fff;
            padding-left: 1.2rem;
            padding-right: 1.2rem;
        }
    }
    &__keyword {
        margin-bottom: 3rem;
        padding-bottom: 3rem;
        border-bottom: 0.2rem solid #fff;
        h3 {
            color: #3f3f3f;
            font-size: var(--font-size-16);
            font-weight: 500;
            margin-bottom: 1rem;
        }
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 2rem;
            padding-bottom: 2rem;
        }
    }
    &__filter {
        h3 {
            color: #3f3f3f;
            font-size: var(--font-size-16);
            font-weight: 500;
            margin-bottom: 1rem;
        }
    }
    &__search {
        width: 100%;
        background-color: #fff;
        height: 4.8rem;
        border-radius: 0.5rem;
        overflow: hidden;
        display: grid;
        grid-template-columns: 1fr 8.5rem;
        @include susy-breakpoint($s-320, $c8) {
            border-radius: 1rem;
            grid-template-columns: 1fr 11.2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: 1fr 6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            grid-template-columns: 1fr 6rem;
        }
        input {
            width: 100%;
            display: block;
            border: 0;
            line-height: 3rem;
            height: 4.8rem;
            background-color: transparent;
            color: #000;
            font-size: var(--font-size-14);
            box-sizing: border-box;
            padding: 0 0.8rem;
            &::placeholder {
                color: #696969;
            }
            @include susy-breakpoint($s-320, $c8) {
                background-color: var(--grey-color-e2);
                border-radius: 0.6rem 0 0 0.6rem;
            }
        }
        button {
            text-align: center;
            display: block;
            width: 8.5rem;
            height: 4.8rem;
            border-radius: 0 0.5rem 0.5rem 0;
            background-color: var(--main-color-500);
            padding: 0;
            border: 0;
            color: #fff;
            font-size: var(--font-size-16);
            transition: all 300ms ease;
            &:hover {
                background-color: var(--main-color-700);
            }
            &:active {
                background-color: var(--main-color-700);
            }
            @include susy-breakpoint($s-320, $c8) {
                width: 11.2rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                width: 6rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 6rem;
            }
        }
    }
    &__select-wrap {
        @extend %site-select;
    }
    &__result {
        margin-bottom: 2rem;
        h3 {
            font-weight: 500;
            font-size: var(--font-size-20);
            padding-left: 0.8rem;
            border-left: solid 0.4rem var(--main-color-500);
        }
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 0;
            height: 6rem;
            background-color: #fff;
            display: flex;
            align-items: center;
            border-radius: 1rem 1rem 0 0;
            padding: 0 2rem;
        }
    }
    &__list-wrap {
        display: grid;
        gap: 1.6rem;
        grid-template-columns: repeat(4, 1fr);
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
            background-color: #fff;
            border-radius: 0 0 1rem 1rem;
            padding: 1rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: 1fr;
            gap: 1rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            grid-template-columns: repeat(2, 1fr);
            gap: 1rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            grid-template-columns: repeat(3, 1fr);
            gap: 1rem;
        }
        @include susy-breakpoint($s-1601, $c24) {
            gap: 1rem;
        }
    }
    &__empty {
        height: 60rem;
        display: flex;
        align-items: center;
        justify-content: center;
        @include susy-breakpoint($s-320, $c8) {
            height: 30rem;
        }
    }
    &__empty-msg {
        img {
            width: 33rem;
            display: block;
            margin-bottom: 2rem;
        }
        p {
            text-align: center;
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
        }
        @include susy-breakpoint($s-320, $c8) {
            img {
                width: 23rem;
            }
        }
    }
    &__list {
        @include susy-breakpoint($s-320, $c8) {
            background-color: #fff;
            border-radius: 1rem;
        }
    }
}

.address-book-staff {
    $self: &;
    background-color: #efefef;
    border-radius: 1rem;
    overflow: hidden;
    &.female {
        #{$self}__header {
            background-color: #ffcdcd;
        }
    }
    &.male {
        #{$self}__header {
            background-color: #b8dada;
        }
    }
    &__header {
        display: flex;
        padding: 1rem 2rem;
        gap: 1.6rem;
        align-items: flex-start;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 2rem;
            padding-top: 2rem;
            padding-bottom: 2rem;
            gap: 1.2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding: 0.8rem 1.6rem;
            gap: 1rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            padding: 0.8rem 1.6rem;
            gap: 1rem;
        }
        @include susy-breakpoint($s-1601, $c24) {
            gap: 1rem;
        }
    }
    &__avatar {
        width: 6rem;
        height: 6rem;
        border: solid 0.2rem #fff;
        overflow: hidden;
        border-radius: 50%;
        img {
            @extend .rsp-img;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 7rem;
            height: 7rem;
            margin-right: 1rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 4.8rem;
            height: 4.8rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 4.8rem;
            height: 4.8rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: 4.8rem;
            height: 4.8rem;
        }
    }
    &__profile {
        h4 {
            font-size: var(--font-size-16);
            line-height: 1.3;
            color: var(--grey-color-52);
            font-weight: 500;
        }
        p {
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
            @include susy-breakpoint($s-960, $c12) {
                font-size: var(--font-size-12);
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: var(--font-size-12);
            }
        }
    }
    &__contact {
        padding: 1rem 1.6rem;
        @include susy-breakpoint($s-320, $c8) {
            gap: 1rem;
            padding-left: 2rem;
            padding-top: 2rem;
            padding-bottom: 2rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            padding: 0.8rem 1.6rem;
        }
        p {
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.3;
            display: flex;
            align-items: center;
            gap: 0.8rem;
            i {
                color: var(--grey-color-ba);
                @include susy-breakpoint($s-320, $c8) {
                    margin-right: 0.8rem;
                    position: relative;
                    transform: translate3d(0,0.1rem,0);
                }
            }
            a {
                color: var(--grey-color-52);
            }
            &:not(:last-child) {
                margin-bottom: 0.4rem;
            }
            @include susy-breakpoint($s-320, $c8) {
                line-height: 1.6;
                font-size: var(--font-size-14);
                gap: 0;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: var(--font-size-12);
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: var(--font-size-12);
            }
        }
    }
}