.rsp-img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
    height: auto;
}

.site-bg {
    &::after {
        background-image: linear-gradient(to bottom, #ff5474, #ff9e67);
        background-attachment: scroll;
        background-position: 0 0;
        background-size: 100% auto;
        width: 100%;
        height: 100%;
        position: fixed;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        pointer-events: none;
        z-index: -1;
        content: "";
    }
}

.site-transparent-block {
    border: solid 0.1rem rgba(255,255,255,0.4);
    border-radius: 1rem;
    box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.3);
    background-color: rgba(241, 241, 241, 0.3);
}

.home-block {
    box-sizing: border-box;
    padding: 1.6rem;
    border-radius: 0.8rem;
    background-color: #fff;
    box-shadow: 0 2px 15px 0 rgba(0,0,0,0.3);
    width: 100%;
    position: relative;
}

.home-block-header {
    display: flex;
    justify-content: space-between;
    margin-bottom: 2rem;
    h2 {
        font-size: var(--font-size-18);
        line-height: 1.6;
        color: var(--grey-color-52);
        font-weight: 500;
    }
}

.home-post-item {
    width: 16.6rem;
    @include susy-breakpoint($s-320, $c8) {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 6rem;
        gap: 0.8rem;
    }
}

.home-post {
    display: block;
    img {
        @extend .rsp-img;
        display: block;
        margin-bottom: 1.8rem;
        @include susy-breakpoint($s-1201, $c18) {
            margin-bottom: 1rem;
        }
    }
    h3 {
        font-size: var(--font-size-14);
        font-weight: 500;
        line-height: 1.6;
        color: #626262;
        margin-bottom: 0.4rem;
    }
    p {
        font-size: var(--font-size-14);
        color: #626262;
        line-height: 1.6;
        margin-bottom: 1.2rem;
    }
    @include susy-breakpoint($s-320, $c8) {
        display: grid;
        grid-template-columns: 10rem 1fr;
        gap: 0.8rem;
        img {
            margin-bottom: 0;
        }
        h3 {
            margin-bottom: 0;
        }
        p {
            margin-bottom: 0;
        }
    }
}

.home-btn-download {
    align-items: center;
    justify-content: center;
    color: #626262;
    font-size: var(--font-size-14);
    display: flex;
    width: 11rem;
    height: 3rem;
    border-radius: 10px;
    background-color: var(--grey-color-dd);
    transition: all 300ms ease;
    line-height: 1.6;
    &:hover {
        background-color: var(--grey-color-ba);
    }
    br {
        display: none;
    }
    @include susy-breakpoint($s-320, $c8) {
        width: 6rem;
        height: 7rem;
        br {
            display: block;
        }
    }
}

.home-scroll-content {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    overflow-x: auto;
    align-items: flex-start;
    gap: 1.2rem;
    flex-direction: column;
    height: 26rem;
    &.empty {
        justify-content: center;
        flex-direction: row;
    }
    &::-webkit-scrollbar {
        height: 0.6rem;
    }
    &::-webkit-scrollbar-thumb {
        height: 0.6rem;
        border-radius: 1rem;
        background-color: var(--grey-color-d8);
    }
    &::-webkit-scrollbar-track {
        background-color: #fff;
        border-radius: 10rem;
    }
    @include susy-breakpoint($s-320, $c8) {
        flex-direction: row;
        gap: 2rem;
        padding-right: 1rem;
        &::-webkit-scrollbar {
            width: 0.4rem;
        }
        &::-webkit-scrollbar-thumb {
            width: 0.4rem;
        }
    }
}

.site-radio-btn {
    input[type="radio"]:checked,
    input[type="radio"]:not(:checked) {
        position: absolute;
        left: -9999px;
    }
    input[type="radio"]:checked + label,
    input[type="radio"]:not(:checked) + label
    {
        position: relative;
        padding-left: 2rem;
        cursor: pointer;
        line-height: 1.6rem;
        display: inline-block;
        color: var(--grey-color-69);
        font-size: var(--font-size-14);
        @include susy-breakpoint($s-768, $c12) {
            padding-left: 1.6rem;
            line-height: 1.2rem;
        }
    }
    input[type="radio"]:checked + label:before,
    input[type="radio"]:not(:checked) + label:before {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 1.6rem;
        height: 1.6rem;
        border: 0.1rem solid var(--grey-color-8c);
        background: #fff;
        @include susy-breakpoint($s-768, $c12) {
            width: 1.2rem;
            height: 1.2rem;
        }
    }
    input[type="radio"]:checked + label:after,
    input[type="radio"]:not(:checked) + label:after {
        content: '';
        width: 1.6rem;
        height: 1.6rem;
        background: var(--main-color-500);
        position: absolute;
        top: 0.1rem;
        left: 0.1rem;
        -webkit-transition: all 0.2s ease;
        transition: all 0.2s ease;
        @include susy-breakpoint($s-768, $c12) {
            width: 1.2rem;
            height: 1.2rem;
        }
    }
    input[type="radio"]:not(:checked) + label:after {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
    }
    input[type="radio"]:checked + label:after {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

%site-select {
    box-sizing: border-box;
    width: 100%;
    height: 4.9rem;
    position: relative;
    border-radius: 0.8rem;
    overflow: hidden;
    @include susy-breakpoint($s-320, $c8) {
        border-radius: 1rem;
        background-color: var(--grey-color-e2);
    }
    &:not(:last-child) {
        margin-bottom: 2.4rem;
    }
    &::after {
        pointer-events: none;
        z-index: 0;
        width: 8rem;
        height: 100%;
        content: "";
        right: 0;
        top: 0;
        position: absolute;
        background-color: var(--main-color-500);
        transition: all 300ms ease;
        @include susy-breakpoint($s-320, $c8) {
            width: 11.2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 6rem;
        }
    }
    &::before {
        pointer-events: none;
        z-index: 10;
        content: "\e915";
        position: absolute;
        right: 3rem;
        top: 50%;
        transform: translate3d(0,-50%,0) rotate(90deg);
        font-family: 'one';
        font-size: var(--font-size-20);
        color: #fff;
        @include susy-breakpoint($s-320, $c8) {
            right: 4.5rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            right: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            right: 2rem;
        }
    }
    select {
        padding: 0 0.8rem;
        -webkit-appearance: none;
        -moz-appearance: none;
        text-indent: 0.1rem;
        box-sizing: border-box;
        padding-right: 8rem;
        width: 100%;
        height: 4.9rem;
        border: 0;
        font-size: var(--font-size-14);
        color: #696969;
        background-color: #fff;
        @include susy-breakpoint($s-320, $c8) {
            padding-right: 11.2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding-right: 6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding-right: 6rem;
        }
    }
    &:hover {
        &::after {
            background-color: var(--grey-color-92);
        }
    }
}

%site-select-search {
    box-sizing: border-box;
    width: 100%;
    height: 4.8rem;
    position: relative;
    border-radius: 0.8rem;
    overflow: hidden;
    @include susy-breakpoint($s-320, $c8) {
        border-radius: 0.8rem;
        height: 4rem;
        background-color: var(--grey-color-e2);
    }
    &:not(:last-child) {
        margin-bottom: 2.4rem;
    }
    &::after {
        pointer-events: none;
        z-index: 0;
        width: 6.6rem;
        height: 100%;
        content: "";
        right: 0;
        top: 0;
        position: absolute;
        background-color: var(--grey-color-af);
        transition: all 300ms ease;
        @include susy-breakpoint($s-960, $c12) {
            width: 5.6rem;
        }
    }
    &::before {
        pointer-events: none;
        z-index: 10;
        content: "\e915";
        position: absolute;
        right: 2.4rem;
        top: 50%;
        transform: translate3d(0,-50%,0) rotate(90deg);
        font-family: 'one';
        font-size: var(--font-size-20);
        color: #fff;
        @include susy-breakpoint($s-960, $c12) {
            right: 1.6rem;
        }
    }
    select {
        padding: 0 0.8rem;
        -webkit-appearance: none;
        -moz-appearance: none;
        text-indent: 0.1rem;
        box-sizing: border-box;
        padding-right: 6.6rem;
        width: 100%;
        height: 4.8rem;
        border: 0;
        font-size: var(--font-size-16);
        color: #696969;
        @include susy-breakpoint($s-320, $c8) {
            padding-right: 6.6rem;
            background-color: transparent;
            height: 4rem;
            font-size: var(--font-size-14);
        }
        @include susy-breakpoint($s-768, $c12) {
            padding-right: 6.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding-right: 5.6rem;
        }
    }
    &:hover {
        &::after {
            background-color: var(--grey-color-92);
        }
    }
}

%page-title {
    font-weight: 500;
    font-size: var(--font-size-20);
    color: #000;
    padding-left: 0.6rem;
    border-left: solid 0.4rem var(--main-color-500);
    line-height: 2rem;
    height: 2rem;
    @include susy-breakpoint($s-320, $c8) {
        margin-left: 1.6rem;
        margin-right: 1.6rem;
    }
}

%site-btn-more {
    background-color: var(--grey-color-dd);
    height: 3rem;
    display: block;
    font-size: var(--font-size-14);
    color: var(--grey-color-62);
    line-height: 3rem;
    border-radius: 0.8rem;
    position: relative;
    padding-right: 5.6rem;
    padding-left: 1.2rem;
    overflow: hidden;
    &::before {
        content: "";
        width: 4.6rem;
        background-color: var(--sub-color-200);
        position: absolute;
        height: 100%;
        top: 0;
        right: 0;
        transition: all 300ms ease;
    }
    &::after {
        pointer-events: none;
        z-index: 10;
        position: absolute;
        right: 1.6rem;
        top: 50%;
        transform: translate3d(0,-50%,0);
        font-family: 'one';
        content: "\e915";
        font-size: var(--font-size-14);
        color: #fff;
        transition: all 300ms ease;
    }
    &:hover {
        &::before {
            background-color: var(--sub-color-350);
        }
        &::after {
            transform: translate3d(20%,-50%,0);
        }
    }
}

%site-search {
    width: 100%;
    background-color: var(--grey-color-dd);
    height: 4.8rem;
    border-radius: 0.8rem;
    display: flex;
    overflow: hidden;
    @include susy-breakpoint($s-768, $c12) {
        height: 3.8rem;
    }
    input {
        border: 0;
        line-height: 3rem;
        height: 4.8rem;
        background-color: transparent;
        color: #000;
        font-size: var(--font-size-16);
        width: calc(100% - 5.7rem);
        box-sizing: border-box;
        padding: 0 0.8rem;
        &::placeholder {
            color: #696969;
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: var(--font-size-14);
            height: 3.8rem;
        }
    }
    button {
        text-align: center;
        display: block;
        width: 8.5rem;
        height: 4.8rem;
        border-radius: 0 0.5rem 0.5rem 0;
        background-color: var(--main-color-500);
        padding: 0;
        border: 0;
        color: #fff;
        font-size: var(--font-size-16);
        transition: all 300ms ease;
        &:hover {
            background-color: var(--main-color-700);
        }
        &:active {
            background-color: var(--main-color-700);
        }
        @include susy-breakpoint($s-768, $c12) {
            height: 3.8rem;
            font-size: var(--font-size-14);
        }
    }
}

%site-btn-video {
    display: block;
    position: relative;
    background-color: var(--grey-color-52);
    overflow: hidden;
    width: 100%;
    padding-bottom: 56.25%;
    &::after {
        width: 18%;
        padding-bottom: 18%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate3d(-50%,-50%,0);
        content: "";
        background: transparent url('../images/icon-play.svg') no-repeat 0 0;
        background-size: 100% auto;
        transition: all 300ms ease-in-out;
    }
    img {
        position: absolute;
        z-index: 0;
        max-width: auto;
        display: block;
        margin: 0;
        height: 100%;
        left: 50%;
        top: 0;
        transform: translate3d(-50%,0,0);
    }
    &:hover {
        &::after {
            transform: translate3d(-50%,-55%,0);
        }
    }
}

%video-info-text {
    font-size: var(--font-size-14);
    color: var(--grey-color-7d);
    line-height: 1.6;
}

%video-info-date {
    &::after {
        content: "/";
        font-size: var(--font-size-12);
        color: var(--grey-color-7d);
        line-height: 1.6;
        padding: 0 0.8rem;
    }
}

%video-content {
    margin-bottom: 0.8rem;
    > p {
        font-size: var(--font-size-16);
        color: var(--grey-color-62);
        line-height: 1.6;
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-14);
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: var(--font-size-14);
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: var(--font-size-14);
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: var(--font-size-14);
        }
    }
}

%video-description {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    > h3 {
        font-size: var(--font-size-18);
        color: var(--grey-color-62);
        line-height: 1.6;
        font-weight: normal;
        margin-bottom: 0.8rem;
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-16);
        }
        @include susy-breakpoint($s-768, $c12) {
            font-size: var(--font-size-16);
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: var(--font-size-16);
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: var(--font-size-16);
        }
    }
}

%video-info {
    margin-bottom: 0;
    margin-top: auto;
}