html {
    font-family: 'Noto Sans TC', sans-serif, "", "Microsoft JhengHei", "Arial Unicode MS", "Helvetica Neue", Helvetica, sans-serif;
}

body {
    background-color: #fff;
    height: 100%;
    &.overflow {
        overflow: hidden;
    }
}

.bg-clouds-left {
    background: transparent url('../images/bg-clouds.png') no-repeat 0 0;
    background-size: cover;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 50.7rem;
    height: 27.4rem;
    transform: translate3d(-30%,60%,0) scaleX(-1);
    z-index: 0;
    pointer-events: none;
    animation-name: cloud-move-left;
    animation-timing-function: linear;
    animation-duration: 20s;
    animation-fill-mode: both;
    animation-direction: alternate;
    animation-iteration-count: infinite;
}

@keyframes cloud-move-left {
    0% {
        transform: translate3d(-30%,60%,0) scaleX(-1);
    }
    100% {
        transform: translate3d(-20%,60%,0) scaleX(-1);
    }
}

.bg-clouds-top {
    background: transparent url('../images/bg-clouds.png') no-repeat 0 0;
    background-size: cover;
    position: fixed;
    left: 0;
    top: 0;
    width: 50.7rem;
    height: 27.4rem;
    opacity: 0.2;
    transform: translate3d(-10%,10%,0) scaleX(-1);
    z-index: 0;
    pointer-events: none;
    animation-name: cloud-move-top;
    animation-timing-function: linear;
    animation-duration: 24s;
    animation-fill-mode: both;
    animation-direction: alternate;
    animation-iteration-count: infinite;
}

@keyframes cloud-move-top {
    0% {
        transform: translate3d(-10%,10%,0) scaleX(-1);
    }
    100% {
        transform: translate3d(0%,10%,0) scaleX(-1);
    }
}

.bg-clouds-right {
    background: transparent url('../images/bg-clouds.png') no-repeat 0 0;
    background-size: cover;
    position: fixed;
    right: 0;
    top: 0;
    width: 50.7rem;
    height: 27.4rem;
    opacity: 0.25;
    transform: translate3d(-10%,10%,0) scaleX(-1);
    z-index: 0;
    pointer-events: none;
    animation-name: cloud-move-right;
    animation-timing-function: linear;
    animation-duration: 20s;
    animation-fill-mode: both;
    animation-direction: alternate;
    animation-iteration-count: infinite;
    @include susy-breakpoint($s-320, $c8) {
        display: none;
    }
}

@keyframes cloud-move-right {
    0% {
        transform: translate3d(-10%,10%,0) scaleX(-1);
    }
    100% {
        transform: translate3d(0%,10%,0) scaleX(-1);
    }
}

.site-pagination {
    padding: 5.4rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1.2rem;
    @include susy-breakpoint($s-320, $c8) {
        padding-top: 3rem;
        padding-bottom: 3rem;
        &.video {
            background-color: #fff;
            border-radius: 0 0 1rem 1rem;
        }
    }
    @include susy-breakpoint($s-768, $c12) {
        padding-top: 3rem;
        padding-bottom: 3rem;
    }
    &__btn-first,
    &__btn-prev,
    &__btn-next,
    &__btn-last {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 4.2rem;
        height: 4.2rem;
        border-radius: 50%;
        background-color: var(--grey-color-dd);
        transition: all 300ms ease;
        i {
            color: var(--main-color-500);
            font-size: var(--font-size-26);
        }
        &:hover {
            background-color: #ccc;
        }
        &.disable {
            pointer-events: none;
            i {
                color: #df828e;
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 3.6rem;
            height: 3.6rem;
            i {
                color: var(--main-color-500);
                font-size: var(--font-size-20);
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 3.6rem;
            height: 3.6rem;
            i {
                color: var(--main-color-500);
                font-size: var(--font-size-20);
            }
        }
    }
    &__page {
        display: flex;
    }
    &__btn-page {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 3.3rem;
        height: 3.3rem;
        border-radius: 50%;
        font-size: 1.4rem;
        color: #000;
        transition: all 300ms ease;
        &.active {
            background-color: var(--grey-color-dd);
        }
        &:hover {
            background-color: var(--grey-color-dd);
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 3rem;
            height: 3rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 3rem;
            height: 3rem;
        }
    }
    @include susy-breakpoint($s-320, $c8) {
        &__btn-first,
        &__btn-last {
            display: none;
        }
    }
    @include susy-breakpoint($s-768, $c12) {
        &__btn-first,
        &__btn-last {
            display: none;
        }
    }
    @include susy-breakpoint($s-960, $c12) {
        &__btn-first,
        &__btn-last {
            display: none;
        }
    }
}