.profile-page {
    height: 100%;
    &__wrap {
        height: 100%;
        display: grid;
        grid-template-columns: 54rem 1fr;
        gap: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: 1fr;
        }
        @include susy-breakpoint($s-960, $c12) {
            grid-template-columns: 30rem 1fr;
        }
        @include susy-breakpoint($s-1201, $c18) {
            grid-template-columns: 48rem 1fr;
        }
    }
    &__avatar-edit {
        border-radius: 1rem;
        background-color: #dedede;
        padding: 2rem 3rem;
        @include susy-breakpoint($s-320, $c8) {
            padding: 2rem;
            border-radius: 0 0 1rem 1rem;
        }
    }
    &__header {
        margin-bottom: 5rem;
        h3 {
            font-weight: 500;
            font-size: var(--font-size-20);
            padding-left: 0.8rem;
            border-left: solid 0.4rem var(--main-color-500);
        }
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 3rem;
        }
    }
    &__content {
        padding: 0 4rem 4rem;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 2rem;
            padding-right: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding-left: 1rem;
            padding-right: 1rem;
        }
    }
    &__avatar {
        width: 14.8rem;
        height: 14.8rem;
        border-radius: 50%;
        overflow: hidden;
        border: solid 0.2rem #fff;
        margin: 0 auto 8rem;
        img {
            max-width: 100%;
            object-fit: cover;
            margin-bottom: 0;
            display: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 3rem;
        }
    }
    &__upload-wrap {
        flex-basis: 32rem;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto 2rem;
        label {
            display: flex;
            justify-items: center;
            align-items: center;
            padding: 0 2rem;
            background-color: var(--main-color-500);
            color: white;
            font-size: var(--font-size-14);
            border-radius: 0 1rem 1rem 0;
            cursor: pointer;
            height: 4.8rem;
        }
        #file-chosen {
            width: 12rem;
            padding: 0 1.6rem;
            display: inline-block;
            margin-left: 0.3rem;
            background-color: #fff;
            height: 4.8rem;
            line-height: 4.8rem;
            border-radius: 1rem 0 0 1rem;
            overflow: hidden;
        }
    }
    &__upload {
        margin-bottom: 4rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 0;
        }
    }
    &__btn-upload,
    &__btn-update {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 13.6rem;
        height: 3.8rem;
        border-radius: 6rem;
        background-color: var(--main-color-500);
        color: #fff;
        font-size: var(--font-size-14);
        text-align: center;
        border: 0;
        margin: 0 auto;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--main-color-700);
        }
        &:active {
            background-color: var(--main-color-700);
        }
    }
    &__btn-upload + span {
        font-size: var(--font-size-14);
        color: #000;
        line-height: 1.6;
        text-align: center;
        display: block;
        padding-top: 1rem;
    }
    &__profile-edit {
        border-radius: 1rem;
        background-color: #ededed;
        padding: 2rem 3rem;
        @include susy-breakpoint($s-320, $c8) {
            padding: 2rem;
            background-color: #fff;
        }
    }
    &__profile-content {
        box-sizing: border-box;
        width: 30rem;
        padding-bottom: 4rem;
        margin: 0 auto;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            padding-bottom: 2rem;
        }
    }
    &__rows {
        display: flex;
        align-items: center;
        gap: 3rem;
        &:not(:last-child) {
            margin-bottom: 3rem;
        }
    }
    &__public {
        display: flex;
        align-items: center;
        gap: 0.4rem;
    }
    &__label {
        color: #000;
        font-size: var(--font-size-14);
        line-height: 1.6;
        font-weight: 700;
    }
    &__value{
        color: #000;
        font-size: var(--font-size-14);
        line-height: 1.6;
    }
}

.image-croppie {
    width: 100%;
    height: 100vh;
    background-color: rgba(0,0,0,0.5);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 200;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: none;
    opacity: 0;
    &.active {
        opacity: 1;
        pointer-events: auto;
    }
    &__inner {
        width: 28rem;
        height: 28rem;
    }
    &__control {
        display: flex;
        justify-content: center;
        padding-top: 6rem;
    }
    &__btn-crop {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 13.6rem;
        height: 3.8rem;
        border-radius: 6rem;
        background-color: var(--main-color-500);
        color: #fff;
        font-size: var(--font-size-14);
        text-align: center;
        border: 0;
        margin: 0 auto;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--main-color-700);
        }
        &:active {
            background-color: var(--main-color-700);
        }
    }
}