.site-nav {
    padding-top: 3rem;
    position: relative;
    z-index: 10;
    @include susy-breakpoint($s-320, $c8) {
        padding-top: 1rem;
        margin-bottom: 2rem;
        z-index: 11;
    }
    &.fullscreen {
        z-index: 0;
    }
    &__header {
        @extend .site-transparent-block;
        box-sizing: border-box;
        padding: 2.6rem 0;
        border-radius: 0 1rem 1rem 0;
        display: flex;
        justify-content: center;
        align-items: center;
        border-left: 0;
        margin-bottom: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            border-radius: 1rem;
            height: 6rem;
            padding: 0 1.6rem;
            display: block;
            margin-bottom: 0;
            transition: all 300ms ease;
            &.active {
                height: 14rem;
                align-items: flex-start;
                border-radius: 1rem 1rem 0 0;
            }
        }
    }
    &__logo {
        width: 16rem;
        height: 8.1rem;
        background: transparent url('../images/firstins-logo.png') no-repeat 0 0;
        background-size: cover;
        font-size: 0;
        @include susy-breakpoint($s-320, $c8) {
            position: absolute;
            top: 2.4rem;
            left: 2rem;
            width: 26.8rem;
            height: 3.6rem;
            background-image: url('../images/firstins-logo-m.png');
            background-size: cover;
            transition: all 300ms ease;
            &.active {
                pointer-events: none;
                opacity: 0;
            }
        }
        a {
            display: block;
            width: 100%;
            height: 100%;
        }
    }
    &__btn-toggle {
        width: 2.4rem;
        height: 2.4rem;
        position: absolute;
        border: 0;
        right: 2rem;
        background-color: transparent;
        display: none;
        top: 2.8rem;
        @include susy-breakpoint($s-320, $c8) {
            display: block;
        }
        &::before,
        &::after {
            font-family: 'one';
            left: 50%;
            top: 50%;
            transform: translate3d(-50%,-50%,0);
            color: #fff;
            width: 100%;
            height: 100%;
            position: absolute;
            font-size: var(--font-size-22);
            transition: all 600ms ease;
        }
        &::before {
            content: "\e909";
        }
        &::after {
            content: "\e903";
            opacity: 0;
        }
        &.active {
            &::before {
                opacity: 0;
            }
            &::after {
                opacity: 1;
            }
        }
    }
    &__nav-list {
        display: block;
        border-radius: 0 1rem 1rem 0;
        background-color: var(--grey-color-f1);
        box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.3);
        padding: 3rem;
        position: relative;
        overflow: hidden;
        li {
            &:not(:last-child) {
                margin-bottom: 1.6rem;
            }
            @include susy-breakpoint($s-320, $c8) {
                &:not(:last-child) {
                    margin-bottom: 0;
                }
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            height: calc(100% - 15rem);
            overflow-y: auto;
            width: calc(100% - 2rem);
            position: fixed;
            top: 15rem;
            left: 50%;
            padding-left: 0;
            padding-right: 0;
            border-radius: 0;
            padding-top: 1rem;
            opacity: 0;
            pointer-events: none;
            transition: opacity 300ms ease;
            z-index: 200;
            border-radius: 0 0 1rem 1rem;
            box-sizing: border-box;
            padding-bottom: 1rem;
            transform: translate3d(-50%,0,0);
            &.active {
                pointer-events: auto;
                opacity: 1;
            }
        }
    }
    &__btn {
        color: #4d4d4d;
        font-size: var(--font-size-18);
        line-height: 1.6;
        transition: all 300ms ease;
        display: flex;
        align-items: center;
        gap: 2rem;
        i {
            font-size: var(--font-size-20);
            pointer-events: none;
        }
        &:hover {
            color: #000;
        }
        &:active {
            color: var(--sub-color-500);
        }
        &.active {
            color: var(--sub-color-500);
        }
        &.disable {
            color: #b9b9b9;
            pointer-events: none;
        }
        @include susy-breakpoint($s-320, $c8) {
            gap: 1rem;
            width: 16rem;
            margin: 0 auto;
            padding-top: 1rem;
            padding-bottom: 1rem;
            font-size: var(--font-size-24);
            i {
                font-size: var(--font-size-24);
                position: relative;
                top: 0.2rem;
            }
        }
    }
    &__sub-nav {
        display: block;
        box-sizing: border-box;
        padding: 3.2rem 2.3rem;
        top: 0;
        position: absolute;
        width: 15.6rem;
        height: 100%;
        background-color: var(--grey-color-48);
        left: 6.8rem;
        transform: translate3d(100%,0,0);
        transition: all 300ms ease;
        &.active {
            transform: translate3d(0,0,0);
        }
        @include susy-breakpoint($s-320, $c8) {
            position: relative;
            width: 100%;
            left: inherit;
            transform: translate3d(0,0,0);
            overflow: hidden;
            height: auto;
            display: none;
            padding: 0;
        }
    }
    &__sub-nav-inner {
        @include susy-breakpoint($s-320, $c8) {
            padding: 2rem;
            gap: 2rem;
            grid-template-columns: repeat(2, 1fr);
            display: grid;
        }
    }
    &__btn-sub {
        box-sizing: border-box;
        display: block;
        font-size: var(--font-size-14);
        color: #fff;
        line-height: 1.6;
        transition: all 300ms ease;
        &:not(:last-child) {
            margin-bottom: 2.2rem;
        }
        &:hover {
            color: #ffb1ba;
        }
        &.active {
            color: #ffb1ba;
        }
        &.disable {
            pointer-events: none;
            color: #868686;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-20);
            flex-basis: 50%;
            &:not(:last-child) {
                margin-bottom: 0;
            }
        }
    }
}