.section-home {
    @extend .site-bg;
    box-sizing: border-box;
    min-height: 100vh;
    padding-bottom: 4.6rem;
    display: grid;
    grid-template-columns: 22.3rem 1fr;
    gap: 2.4rem;
    position: relative;
    @include susy-breakpoint($s-320, $c8) {
        padding-left: 1rem;
        padding-right: 1rem;
        display: block;
        min-height: inherit;
        padding-bottom: 2rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        gap: 2rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        gap: 2rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        gap: 2rem;
    }
    // &.meeting-room {
    //     overflow: hidden;
    // }
}

.site-container {
    box-sizing: border-box;
    position: relative;
    z-index: 10;
    margin: 0 auto;
    width: 130rem;
    display: grid;
    padding-top: 9.6rem;
    grid-template-columns: 1fr 1fr 25rem;
    grid-template-rows: 35rem 35rem;
    gap: 2.4rem;
    align-items: flex-start;
    @include susy-breakpoint($s-320, $c8) {
        width: 100%;
        padding-top: 0;
        grid-template-columns: minmax(0, 1fr);
        grid-template-rows: minmax(0, 1fr);
        gap: 2rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        width: 100%;
        grid-template-columns: minmax(0, 1fr);
        grid-template-rows: minmax(0, 1fr);
        padding-right: 2rem;
        gap: 2rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        width: 100%;
        grid-template-columns: minmax(0, 1fr);
        grid-template-rows: minmax(0, 1fr);
        padding-right: 2rem;
        gap: 2rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        width: 100%;
        padding-right: 2rem;
        gap: 2rem;
        grid-template-rows: 35rem 39rem;
    }
}

.site-page-container {
    position: relative;
    z-index: 10;
    margin: 0 auto;
    width: 130rem;
    padding-top: 4rem;
    box-sizing: border-box;
    @include susy-breakpoint($s-320, $c8) {
        width: 100%;
        padding-top: 0;
    }
    @include susy-breakpoint($s-768, $c12) {
        width: 100%;
        padding-right: 2rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        width: 100%;
        padding-right: 2rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        width: 100%;
        padding-right: 2rem;
    }
    &__nav {
        display: flex;
        gap: 1.6rem;
        position: relative;
        z-index: 10;
        overflow: hidden;
        position: relative;
        @include susy-breakpoint($s-320, $c8) {
            gap: 0.8rem;
        }
        a {
            box-sizing: border-box;
            font-weight: 500;
            display: inline-block;
            border-radius: 1rem 1rem 0 0;
            background-color: var(--grey-color-dd);
            width: 29rem;
            padding: 1.6rem 0 1.6rem 3rem;
            font-size: var(--font-size-24);
            color: #888;
            transform: translateY(10%);
            box-shadow: inset 0 -0.4rem 0.8rem 0 rgba(0, 0, 0, 0.15);
            transition: all 300ms ease;
            @include susy-breakpoint($s-320, $c8) {
                width: 100%;
                padding-left: 1.2rem;
                font-size: var(--font-size-20);
                height: 5.2rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                font-size: var(--font-size-20);
                width: 20rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 24rem;
                font-size: var(--font-size-20);
            }
            @include susy-breakpoint($s-1201, $c18) {
                width: 26rem;
            }
            &:hover {
                transform: translateY(0);
            }
            &.active {
                box-shadow: inset 0 2px 15px 0 rgba(0, 0, 0, 0);
                position: relative;
                transform: translateY(0);
                pointer-events: none;
                background-color: #fff;
            }
        }
    }
    &__title {
        position: relative;
        z-index: 0;
        span {
            box-sizing: border-box;
            box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.3);
            font-weight: 500;
            display: inline-block;
            border-radius: 1rem 1rem 0 0;
            background-color: var(--grey-color-f1);
            width: 29rem;
            padding: 1.6rem 0 1.6rem 3rem;
            font-size: var(--font-size-24);
            color: #888;
            @include susy-breakpoint($s-320, $c8) {
                width: 100%;
                padding-left: 1.2rem;
                font-size: var(--font-size-20);
                height: 5.2rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                width: 24rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 24rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                width: 26rem;
            }
        }
    }
    &__container {
        box-sizing: border-box;
        padding: 3rem;
        box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.3);
        z-index: 1;
        position: relative;
        background-color: #fff;
        border-radius: 0 1rem 1rem 1rem;
        min-height: 66.6rem;
        display: grid;
        grid-template-columns: minmax(0, 1fr);
        grid-template-rows: auto;
        @include susy-breakpoint($s-320, $c8) {
            min-height: min-content;
            padding: 0;
            border-radius: 0 0 1rem 1rem;
            min-height: inherit;
            &.address-book,
            &.profile,
            &.video,
            &.meeting-room {
                background-color: transparent;
                box-shadow: none;
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            padding: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding: 2rem;
            min-height: 67.1rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            padding: 2rem;
        }
    }
}

.home-event-course-block {
    height: 100%;
    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
    @include susy-breakpoint($s-320, $c8) {
        grid-column-start: inherit;
        grid-column-end: inherit;
        grid-row-start: inherit;
        grid-row-end: inherit;
        height: 33rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        grid-column-start: inherit;
        grid-column-end: inherit;
        grid-row-start: inherit;
        grid-row-end: inherit;
    }
    @include susy-breakpoint($s-960, $c12) {
        grid-column-start: inherit;
        grid-column-end: inherit;
        grid-row-start: inherit;
        grid-row-end: inherit;
    }
    &__tabs {
        display: none;
        gap: 0.8rem;
        @include susy-breakpoint($s-320, $c8) {
            display: flex;
            position: relative;
            z-index: 10;
        }
    }
    &__btn-tab {
        width: 11.4rem;
        height: 3.4rem;
        border-radius: 1rem 1rem 0 0;
        background-color: var(--grey-color-d7);
        border: 0;
        font-size: var(--font-size-18);
        color: var(--grey-color-52);
        transition: all 300ms ease;
        &.active {
            background-color: #fff;
            pointer-events: none;
        }
    }
    &__inner {
        height: 100%;
        display: grid;
        gap: 2.4rem;
        grid-template-columns: repeat(2, 1fr);
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
            gap: 0;
            background-color: #fff;
            border-radius: 0 1rem 1rem 1rem;
            position: relative;
            height: calc(100% - 3.4rem);
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: 1fr;
            gap: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            grid-template-columns: 1fr;
            gap: 2rem;
        }
    }
}

.home-event {
    @extend .home-block;
    height: 100%;
    @include susy-breakpoint($s-320, $c8) {
        position: absolute;
        width: 100%;
        left: 0;
        right: 0;
        opacity: 0;
        pointer-events: none;
        transition: all 300ms ease;
        border-radius: 0 0.8rem 0.8rem 0.8rem;
        &.active {
            pointer-events: auto;
            opacity: 1;
        }
    }
    &__header {
        @extend .home-block-header;
        @include susy-breakpoint($s-320, $c8) {
            display: none;
        }
    }
    &__content {
        @extend .home-scroll-content;
    }
    &__post-item {
        @extend .home-post-item;
    }
    &__btn-post {
        @extend .home-post;
    }
    &__btn-download {
        @extend .home-btn-download;
    }
    &__empty {
        img {
            display: block;
            width: 9.8rem;
            margin: 0 auto 1rem;
        }
        p {
            font-size: var(--font-size-18);
            line-height: 1.6;
            text-align: center;
            color: var(--grey-color-ba);
        }
    }
}

