.home-board {
    $self: &;
    @extend .home-block;
    overflow: hidden;
    padding: 0;
    @include susy-breakpoint($s-320, $c8) {
        height: 8.65rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 2;
    }
    @include susy-breakpoint($s-960, $c12) {
        grid-column-start: 2;
        grid-column-end: 3;
        grid-row-start: 1;
        grid-row-end: 2;
    }
    &__content {
        height: 100%;
    }
    &__btn {
        display: grid;
        grid-template-columns: 1fr 10rem;
        height: 100%;
        &:hover {
            #{$self}__title {
                background-color: var(--grey-color-dd);
            }
            #{$self}__newest {
                background-color: #f47784;
            }
        }
    }
    &__title {
        background-color: #fff;
        flex-grow: 1;
        padding: 1.6rem;
        transition: all 300ms ease;
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 0.2rem;
        h2 {
            color: var(--grey-color-52);
            font-weight: 500;
            font-size: var(--font-size-16);
            flex-basis: 100%;
        }
        p {
            font-size: var(--font-size-14);
            color: #949494;
            flex-basis: 100%;
            position: relative;
            transform: translate3d(0,-5%,0);
        }
    }
    &__newest {
        flex-basis: 10.5rem;
        background-color: var(--sub-color-300);
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all 300ms ease;
        @include susy-breakpoint($s-320, $c8) {
            flex-basis: 15rem;
        }
        span {
            color: #fff;
            font-size: var(--font-size-14);
        }
        strong {
            font-size: 4.5rem;
            font-weight: 500;
        }
    }
}

