.home-reservation {
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
    height: 100%;
    @include susy-breakpoint($s-320, $c8) {
        grid-column-start: inherit;
        grid-column-end: inherit;
        grid-row-start: inherit;
        grid-row-end: inherit;
    }
    @include susy-breakpoint($s-768, $c12) {
        grid-column-start: inherit;
        grid-column-end: inherit;
        grid-row-start: inherit;
        grid-row-end: inherit;
    }
    @include susy-breakpoint($s-960, $c12) {
        grid-column-start: inherit;
        grid-column-end: inherit;
        grid-row-start: inherit;
        grid-row-end: inherit;
    }
    &__inner {
        @extend .home-block;
        height: 100%;
    }
    &__header {
        @extend .home-block-header;
    }
    &__form {
        box-sizing: border-box;
        display: grid;
        grid-template-columns: 1fr 9.4rem;
        gap: 1.4rem;
        padding-bottom: 2.4rem;
        border-bottom: 0.2rem solid var(--grey-color-e4);
        margin-bottom: 2.4rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr 7.4rem;
        }
    }
    &__timepicker-wrap {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
        }
        @include susy-breakpoint($s-1201, $c18) {
            grid-template-columns: 1fr;
        }
    }
    &__datepicker,
    &__timepicker {
        box-sizing: border-box;
        background-color: var(--grey-color-dd);
        border-radius: 1rem;
        height: 5rem;
        overflow: hidden;
        display: grid;
        grid-template-columns: 1fr 7.2rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr 7.4rem;
        }
        input {
            width: 100%;
            box-sizing: border-box;
            background: transparent;
            line-height: 5rem;
            border: 0;
            font-size: var(--font-size-16);
            color: #000;
            padding: 0 1.2rem;
            display: block;
            &::placeholder {
                color: var(--grey-color-52);
                font-size: var(--font-size-16);
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: var(--font-size-14);
                &::placeholder {
                    font-size: var(--font-size-14);
                }
            }
        }
    }
    &__btn-pick {
        cursor: pointer;
        display: block;
        width: 100%;
        border: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: var(--grey-color-bf);
        font-size: var(--font-size-16);
        color: #3f3f3f;
        transition: all 300ms ease;
        @include susy-breakpoint($s-1201, $c18) {
            font-size: var(--font-size-14);
        }
        &:hover {
            background-color: var(--grey-color-a9);
        }
    }
    &__datepicker {
        margin-bottom: 1rem;
    }
    &__btn-submut {
        box-sizing: border-box;
        display: flex;
        width: 100%;
        height: 100%;
        justify-content: center;
        align-items: center;
        background-color: var(--main-color-500);
        transition: all 300ms ease;
        border-radius: 1rem;
        border: 0;
        &:hover {
            background-color: var(--main-color-700);
        }
        &:active {
            background-color: var(--main-color-700);
        }
        span {
            font-size: var(--font-size-16);
            color: #fff;
            line-height: 1.2;
        }
    }
    &__status-wrap {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 2rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
        }
        @include susy-breakpoint($s-1201, $c18) {
            gap: 1rem;
        }
    }
    &__status-item {
        border-radius: 1rem;
        height: 100%;
        overflow: hidden;
        display: grid;
        grid-template-columns: 1fr 10rem;
        @include susy-breakpoint($s-1201, $c18) {
            grid-template-columns: 1fr 7rem;
        }
    }
    &__location {
        padding: 1.6rem;
        background-color: var(--grey-color-dd);
        @include susy-breakpoint($s-1201, $c18) {
            padding: 1rem;
        }
        h2 {
            color: var(--grey-color-52);
            font-weight: 500;
            font-size: var(--font-size-18);
            line-height: 1.6;
            text-align: center;
            margin-bottom: 0.4rem;
            @include susy-breakpoint($s-1201, $c18) {
                font-size: var(--font-size-16);
                margin-bottom: 0;
            }
        }
        p {
            font-size: var(--font-size-14);
            color: #949494;
            text-align: center;
            line-height: 1.6;
        }
    }
    &__status {
        background-color: var(--sub-color-300);
        display: flex;
        justify-content: center;
        align-items: center;
        @include susy-breakpoint($s-320, $c8) {
            flex-basis: 16.3rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            flex-basis: 6rem;
        }
        span {
            color: #fff;
            font-size: var(--font-size-14);
        }
        strong {
            font-size: 4.5rem;
            font-weight: 500;
            @include susy-breakpoint($s-1201, $c18) {
                font-size: var(--font-size-32);
            }
        }
    }
}

