        grid-row-start: inherit;
        grid-row-end: inherit;
    }
    &__inner {
        @extend .home-block;
        gap: 2.4rem;
        display: grid;
        grid-template-columns: 1fr 20rem;
        height: 100%;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
        }
        @include susy-breakpoint($s-1201, $c18) {
            grid-template-columns: 1fr 22rem;
            gap: 2rem;
        }
    }
    &__video {
        position: relative;
        padding-bottom: 52.73%;
        height: 0;
        > div {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
        }
    }
    &__video-description {
        h2 {
            font-size: var(--font-size-18);
            line-height: 1.6;
            margin-bottom: 1rem;
            font-weight: 500;
            color: var(--grey-color-52);
        }
    }
    &__content {
        overflow-y: auto;
        height: 25rem;
        @include susy-breakpoint($s-320, $c8) {
            height: auto;
        }
        @include susy-breakpoint($s-1201, $c18) {
            height: 20rem;
        }
        &::-webkit-scrollbar {
            width: 0.6rem;
            height: 0.6rem;
        }
        &::-webkit-scrollbar-thumb {
            width: 0.6rem;
            height: 0.6rem;
            border-radius: 1rem;
            background-color: var(--grey-color-d8);
        }
        &::-webkit-scrollbar-track {
            background-color: #fff;
            border-radius: 100px;
        }
        p {
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 2rem;
            }
        }
    }
}

.swiper {
    width: 100%;
    height: 100%;
    @include susy-breakpoint($s-320, $c8) {
        width: 100%;
    }
}

.home-video-new {
    grid-column-start: 2;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 3;
    height: 100%;
    width: 100%;
    @include susy-breakpoint($s-320, $c8) {
        grid-column-start: inherit;
        grid-column-end: inherit;
        grid-row-start: inherit;
        grid-row-end: inherit;
        height: auto;
    }
    @include susy-breakpoint($s-768, $c12) {
        grid-column-start: inherit;
        grid-column-end: inherit;
        grid-row-start: inherit;
        grid-row-end: inherit;
    }
    @include susy-breakpoint($s-960, $c12) {
        grid-column-start: inherit;
        grid-column-end: inherit;
        grid-row-start: inherit;
        grid-row-end: inherit;
    }
    &__inner {
        @extend .home-block;
        height: 100%;
    }
    &__header {
        @extend .home-block-header;
    }
    &__btn-more {
        @extend %site-btn-more;
    }
    &__wrap {
        display: grid;
        grid-template-columns: 60% auto;
        gap: 3.2rem;
        padding: 0 5.6rem 4.4rem;
        box-sizing: border-box;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
            padding: 0 3.2rem 3.2rem;
            gap: 0.8rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: 1fr;
            padding: 0 3.2rem 3.2rem;
            gap: 0.8rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 2.4rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            grid-template-columns: 55% auto;
            gap: 2.4rem;
        }
    }
    &__slider {
        overflow: hidden;
        position: relative;
        height: 26.8rem;
        width: 100%;
        @include susy-breakpoint($s-320, $c8) {
            height: auto;
        }
        @include susy-breakpoint($s-768, $c12) {
            height: auto;
        }
        .swiper-button-prev {
            outline: none;
            top: 40%;
            &::after {
                font-family: 'one';
                content: "\e912";
                font-size: var(--font-size-40);
                color: var(--main-color-500);
            }
            @include susy-breakpoint($s-320, $c8) {
                top: 0;
                transform: translate3d(0,190%,0);
                left: -0.4rem;
                &::after {
                    font-size: var(--font-size-32);
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                top: 0;
                transform: translate3d(0,260%,0);
                left: -0.4rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                top: 36%;
            }
        }
        .swiper-button-next {
            outline: none;
            top: 40%;
            &::after {
                font-family: 'one';
                content: "\e915";
                font-size: var(--font-size-40);
                color: var(--main-color-500);
            }
            @include susy-breakpoint($s-320, $c8) {
                top: 0;
                transform: translate3d(0,190%,0);
                right: -0.4rem;
                &::after {
                    font-size: var(--font-size-32);
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                top: 0;
                transform: translate3d(0,260%,0);
                right: -0.4rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                top: 36%;
            }
        }
        .swiper-pagination-bullet {
            width: 1rem;
            height: 1rem;
            background-color: var(--grey-color-c8);
            opacity: 1;
        }
        .swiper-pagination-bullet-active {
            background-color: var(--main-color-500);
        }
    }
    &__btn-video {
        @extend %site-btn-video;
    }
    &__video-description {
        @extend %video-description;
    }
    &__content {
        @extend %video-content;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
        white-space: normal;
        @include susy-breakpoint($s-320, $c8) {
            -webkit-line-clamp: 5;
        }
        @include susy-breakpoint($s-768, $c12) {
            -webkit-line-clamp: 3;
        }
    }
    &__info {
        margin-bottom: 0;
        margin-top: auto;
    }
    &__unit,
    &__date,
    &__type {
        @extend %video-info-text;
    }
    &__date {
        @extend %video-info-date;
    }
}

.home-search {
    width: 16.8rem;
    background-color: var(--grey-color-dd);
    height: 3rem;
    border-radius: 0.5rem;
    display: flex;
    &--admin {
        position: fixed;
        right: 6rem;
        top: 11.6rem;
        width: 30rem;
        z-index: 40;
    }
    input {
        border: 0;
        line-height: 3rem;
        height: 3rem;
        background-color: transparent;
        color: #000;
        font-size: var(--font-size-14);
        width: calc(100% - 5.7rem);
        box-sizing: border-box;
        padding: 0 0.8rem;
    }
    button {
        text-align: center;
        display: block;
        width: 6.2rem;
        height: 3rem;
        border-radius: 0 0.5rem 0.5rem 0;
        background-color: var(--main-color-500);
        padding: 0;
        border: 0;
        color: #fff;
        font-size: var(--font-size-14);
        transition: all 300ms ease;
        &:hover {
            background-color: var(--main-color-700);
        }
        &:active {
            background-color: var(--main-color-700);
        }
    }
}