.video-page {
    padding: 0 2.4rem;
    @include susy-breakpoint($s-320, $c8) {
        padding: 0;
    }
    @include susy-breakpoint($s-768, $c12) {
        padding: 0;
    }
    @include susy-breakpoint($s-960, $c12) {
        padding: 0;
    }
}

.video-nav-wrap {
    display: grid;
    grid-template-columns: 1fr 23rem;
    gap: 3.6rem;
    margin-bottom: 2.4rem;
    @include susy-breakpoint($s-320, $c8) {
        grid-template-columns: 1fr;
        background-color: var(--grey-color-c9);
        padding: 1.6rem;
        gap: 1.6rem;
        margin-bottom: 2rem;
        border-radius: 0 0 1rem 1rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        grid-template-columns: 1fr 18rem;
        gap: 0.8rem;
    }
    &__search {
        @extend %site-search;
        @include susy-breakpoint($s-320, $c8) {
            input {
                background-color: #fff;
            }
        }
    }
}

.video-nav {
    width: 100%;
    background-color: var(--grey-color-dd);
    border-radius: 0.8rem;
    overflow: hidden;
    display: grid;
    grid-template-columns: 3.2rem 1fr 3.2rem;
    padding: 0.8rem;
    gap: 0.8rem;
    box-sizing: border-box;
    position: relative;
    align-items: center;
    @include susy-breakpoint($s-320, $c8) {
        grid-template-columns: 1fr;
        background-color: transparent;
        padding: 0;
        margin-bottom: 0;
        border-radius: 0;
    }
    @include susy-breakpoint($s-768, $c12) {
        grid-template-columns: 2.4rem 1fr 2.4rem;
        padding: 0.4rem;
    }
    &__btn-prev,
    &__btn-next {
        padding: 0;
        width: 3.2rem;
        height: 3.2rem;
        background-color: transparent;
        position: relative;
        border: 0;
        &::before {
            font-size: var(--font-size-24);
            color: var(--main-color-500);
        }
        @include susy-breakpoint($s-320, $c8) {
            display: none;
        }
        @include susy-breakpoint($s-768, $c12) {
            width: 2.4rem;
            height: 2.4rem;
        }
    }
    &__btn-prev {
        &::before {
            font-family: "ONE";
            content: "\e912";
            @include susy-breakpoint($s-768, $c12) {
                font-size: var(--font-size-14);
            }
        }
    }
    &__btn-next {
        &::before {
            font-family: "ONE";
            content: "\e915";
            @include susy-breakpoint($s-768, $c12) {
                font-size: var(--font-size-14);
            }
        }
    }
    &__nav-wrap {
        overflow-x: auto;
        width: auto;
        display: flex;
        gap: 0.8rem;
        scroll-behavior: smooth;
        -ms-overflow-style: none;
        scrollbar-width: none;
        &::-webkit-scrollbar {
            display: none;
        }
        @include susy-breakpoint($s-320, $c8) {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
        }
    }
    &__btn-link {
        font-size: var(--font-size-14);
        color: var(--grey-color-52);
        padding: 0.8rem 1.6rem;
        background-color: #fff;
        border-radius: 0.4rem;
        white-space: nowrap;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--grey-color-ba);
        }
        &.active {
            background-color: var(--sub-color-400);
            color: #fff;
            &:hover {
                background-color: var(--main-color-400);
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            text-align: center;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding: 0.8rem;
        }
    }
}

.video-newest {
    $self: &;
    padding-bottom: 3.6rem;
    margin-bottom: 3.6rem;
    border-bottom: 0.1rem solid var(--grey-color-97);
    @include susy-breakpoint($s-320, $c8) {
        background-color: #fff;
        border-radius: 1rem;
        padding: 2rem;
        margin-bottom: 2rem;
        border-bottom: 0;
    }
    &__header {
        display: flex;
        justify-content: space-between;
        margin-bottom: 1.6rem;
        align-items: center;
    }
    &__title {
        @extend %page-title;
        @include susy-breakpoint($s-320, $c8) {
            margin-left: 0;
        }
    }
    &__btn-more {
        @extend %site-btn-more;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        gap: 2.4rem 8rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
            grid-template-rows: 1fr;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: 1fr;
            grid-template-rows: 1fr;
        }
    }
    &__video-post {
        &:nth-child(1) {
            position: relative;
            grid-column-start: 1;
            grid-column-end: 2;
            grid-row-start: 1;
            grid-row-end: 3;
            height: 100%;
            display: flex;
            flex-direction: column;
            #{$self}__video-description {
                height: 100%;
                padding-top: 0.8rem;
                @include susy-breakpoint($s-320, $c8) {
                    padding-top: 0;
                }
            }
            &::after {
                position: absolute;
                content: "";
                width: 0.1rem;
                height: 100%;
                background-color: var(--grey-color-97);
                top: 0;
                right: -4rem;
            }
            @include susy-breakpoint($s-320, $c8) {
                grid-column-start: initial;
                grid-column-end: initial;
                grid-row-start: initial;
                grid-row-end: initial;
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                gap: 1.6rem;
                &::after {
                    display: none;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                grid-column-start: initial;
                grid-column-end: initial;
                grid-row-start: initial;
                grid-row-end: initial;
                &::after {
                    display: none;
                }
            }
        }
        &:nth-child(2) {
            grid-column-start: 2;
            grid-column-end: 3;
            grid-row-start: 1;
            grid-row-end: 1;
            display: grid;
            grid-template-columns: 60% 1fr;
            gap: 1.6rem;
            @include susy-breakpoint($s-320, $c8) {
                grid-column-start: initial;
                grid-column-end: initial;
                grid-row-start: initial;
                grid-row-end: initial;
                grid-template-columns: repeat(2, 1fr);
            }
            @include susy-breakpoint($s-768, $c12) {
                grid-column-start: initial;
                grid-column-end: initial;
                grid-row-start: initial;
                grid-row-end: initial;
                grid-template-columns: repeat(2, 1fr);
            }
        }
        &:nth-child(3) {
            grid-column-start: 2;
            grid-column-end: 3;
            grid-row-start: 2;
            grid-row-end: 2;
            display: grid;
            grid-template-columns: 60% 1fr;
            gap: 1.6rem;
            @include susy-breakpoint($s-320, $c8) {
                grid-column-start: initial;
                grid-column-end: initial;
                grid-row-start: initial;
                grid-row-end: initial;
                grid-template-columns: repeat(2, 1fr);
            }
            @include susy-breakpoint($s-768, $c12) {
                grid-column-start: initial;
                grid-column-end: initial;
                grid-row-start: initial;
                grid-row-end: initial;
                grid-template-columns: repeat(2, 1fr);
            }
        }
    }
    &__btn-video {
        @extend %site-btn-video;
    }
    &__video-description {
        @extend %video-description;
    }
    &__content {
        @extend %video-content;
    }
    &__info {
        @extend %video-info;
    }
    &__unit,
    &__date,
    &__type {
        @extend %video-info-text;
    }
    &__date {
        @extend %video-info-date;
    }
}

.video-type {
    @include susy-breakpoint($s-320, $c8) {
        background-color: #fff;
        border-radius: 1rem;
        padding: 2rem;
    }
    &:not(:last-child) {
        padding-bottom: 3.6rem;
        margin-bottom: 3.6rem;
        border-bottom: 0.1rem solid var(--grey-color-97);
        @include susy-breakpoint($s-320, $c8) {
            padding-bottom: 2rem;
            margin-bottom: 2rem;
            border-bottom: 0;
        }
    }
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 1.6rem;
    }
    &__title {
        @extend %page-title;
        @include susy-breakpoint($s-320, $c8) {
            margin-left: 0;
        }
    }
    &__btn-more {
        @extend %site-btn-more;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        gap: 2.4rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: repeat(3, 1fr);
        }
        @include susy-breakpoint($s-960, $c12) {
            grid-template-columns: repeat(3, 1fr);
        }
    }
    &__video-post {
        @include susy-breakpoint($s-320, $c8) {
            display: none;
            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3) {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                gap: 1.6rem;
            }
        }
    }
    &__btn-video {
        @extend %site-btn-video;
    }
    &__video-description {
        @extend %video-description;
    }
    &__content {
        @extend %video-content;
    }
    &__info {
        @extend %video-info;
    }
    &__unit,
    &__date,
    &__type {
        @extend %video-info-text;
    }
    &__date {
        @extend %video-info-date;
    }
}

.video-type-page {
    min-height: 48rem;
    @include susy-breakpoint($s-320, $c8) {
        background-color: #fff;
        border-radius: 1rem 1rem 0 0;
        padding: 2rem 2rem 0;
        min-height: 30rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        min-height: 48rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        min-height: 38rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        min-height: 38rem;
    }
    &__header {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 1.6rem;
    }
    &__title {
        @extend %page-title;
        @include susy-breakpoint($s-320, $c8) {
            margin-left: 0;
        }
    }
    &__btn-more {
        @extend %site-btn-more;
    }
    &__wrap {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        gap: 4rem 2.4rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
            gap: 2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: repeat(3, 1fr);
            gap: 2.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            grid-template-columns: repeat(3, 1fr);
        }
    }
    &__video-post {
        @include susy-breakpoint($s-320, $c8) {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 1.6rem;
        }
    }
    &__btn-video {
        @extend %site-btn-video;
        margin-bottom: 0.8rem;
    }
    &__video-description {
        @extend %video-description;
    }
    &__content {
        @extend %video-content;
    }
    &__info {
        @extend %video-info;
    }
    &__unit,
    &__date,
    &__type {
        @extend %video-info-text;
    }
    &__date {
        @extend %video-info-date;
    }
    &__empty {
        min-height: 48rem;
        display: flex;
        justify-content: center;
        align-items: center;
        @include susy-breakpoint($s-320, $c8) {
            min-height: 40rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            min-height: 40rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            min-height: 40rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            min-height: 40rem;
        }
        img {
            width: 31.5rem;
            display: block;
            margin: 0 auto 1.6rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 28rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                width: 28rem;
            }
        }
        p {
            text-align: center;
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
        }
    }
}

.video-detail-page {
    padding: 0 2.4rem;
    display: grid;
    grid-template-columns: minmax(0, 1fr) 30rem;
    gap: 4rem;
    @include susy-breakpoint($s-320, $c8) {
        grid-template-columns: minmax(0, 1fr);
        gap: 2rem;
        padding: 0;
    }
    @include susy-breakpoint($s-768, $c12) {
        grid-template-columns: minmax(0, 1fr);
        gap: 2rem;
        padding: 0;
    }
    @include susy-breakpoint($s-960, $c12) {
        grid-template-columns: minmax(0, 1fr) 24rem;
        padding: 0;
    }
}

.video-detail {
    width: 100%;
    height: 100%;
    &__video-wrap {
        @include susy-breakpoint($s-320, $c8) {
            background-color: #fff;
            border-radius: 0 0 1rem 1rem;
            padding: 2rem;
        }
    }
    &__header {
        margin-bottom: 1.6rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 0.8rem;
        }
    }
    &__btn-back {
        font-size: var(--font-size-20);
        color: var(--grey-color-52);
        position: relative;
        padding-left: 2.4rem;
        line-height: 1.6;
        @include susy-breakpoint($s-320, $c8) {
            font-size: var(--font-size-16);
            padding-left: 2rem;
        }
        &::before {
            transform: translate3d(0,0,0);
            top: 0;
            left: 0;
            position: absolute;
            content: "\e912";
            font-family: "one";
            font-size: var(--font-size-20);
            color: var(--main-color-500);
            transition: all 300ms ease;
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-16);
            }
        }
        &:hover {
            &::before {
                transform: translate3d(-10%,0,0);
            }
        }
    }
    &__video-player {
        // position: relative;
        // padding-bottom: 56.25%;
        // height: 0;
        margin-bottom: 1.6rem;
        // > div {
        //     position: absolute;
        //     left: 0;
        //     top: 0;
        //     width: 100%;
        //     height: 100%;
        // }
        // @include susy-breakpoint($s-320, $c8) {
        //     margin-bottom: 1.6rem;
        // }
    }
    &__video-description {
        @extend %video-description;
        h3 {
            font-size: var(--font-size-24);
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-20);
            }
        }
    }
    &__content {
        @extend %video-content;
    }
    &__info {
        @extend %video-info;
        margin-bottom: 2.4rem;
    }
    &__unit,
    &__date,
    &__type {
        @extend %video-info-text;
    }
    &__date {
        @extend %video-info-date;
        &::after {
            display: none;
        }
    }
    &__unit {
        font-size: var(--font-size-18);
    }
    &__details {
        margin-bottom: 1.2rem;
    }
    &__tags {
        padding: 1.2rem 2.4rem;
        display: inline-block;
        background-color: var(--grey-color-e8);
        border-radius: 0.8rem;
        @include susy-breakpoint($s-320, $c8) {
            gap: 1.2rem;
            display: flex;
            flex-wrap: wrap;
            padding-left: 1.2rem;
            padding-right: 1.2rem;
        }
        a {
            line-height: 1.6;
            display: inline-block;
            font-size: 1.6rem;
            color: var(--main-color-300);
            transition: color 300ms ease;
            &:not(:last-child) {
                margin-right: 1.2rem;
                @include susy-breakpoint($s-320, $c8) {
                    margin-right: 0;
                }
            }
            &:hover {
                color: var(--main-color-500);
            }
        }
    }
}

.video-recommend {
    background-color: var(--grey-color-ec);
    padding: 2rem;
    border-radius: 0.8rem;
    &__wrap {
        display: flex;
        flex-direction: column;
        gap: 4rem;
        @include susy-breakpoint($s-320, $c8) {
            gap: 2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            gap: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 2rem;
        }
    }
    &__video-post {
        @include susy-breakpoint($s-320, $c8) {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 1.6rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 2rem;
            padding: 0;
        }
    }
    &__title {
        font-size: var(--font-size-20);
        color: var(--grey-color-52);
        line-height: 1.6;
        font-weight: 500;
        margin-bottom: 1.6rem;
    }
    &__btn-video {
        @extend %site-btn-video;
        margin-bottom: 0.8rem;
    }
    &__video-description {
        @extend %video-description;
    }
    &__content {
        @extend %video-content;
    }
    &__info {
        @extend %video-info;
    }
    &__unit,
    &__date,
    &__type {
        @extend %video-info-text;
    }
}