.board-page {
    position: relative;
    min-height: 64rem;
    @include susy-breakpoint($s-320, $c8) {
        min-height: inherit;
    }
    &__search-result {
        @extend %page-title;
        margin-bottom: 2.4rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 2rem;
        }
    }
    &__table {
        width: 100%;
        border-radius: 1rem;
        overflow: hidden;
        @include susy-breakpoint($s-320, $c8) {
            border-radius: 0;
        }
        tr {
            transition: all 300ms ease;
            &:nth-child(odd) {
                background-color: var(--grey-color-ee);
            }
            &:nth-child(even) {
                background-color: var(--grey-color-f9);
            }
            &:hover {
                background-color: var(--grey-color-dd);
            }
        }
        th {
            font-weight: normal;
            background-color: var(--grey-color-ba);
            font-size: var(--font-size-14);
            color: #fff;
            line-height: 1.6;
            padding: 1.8rem 4rem;
            vertical-align: middle;
            &:nth-child(1) {
                text-align: left;
            }
            &:last-child {
                width: 12rem;
            }
            @include susy-breakpoint($s-320, $c8) {
                background-color: var(--grey-color-9c);
                padding: 1rem 1.2rem;
                &:nth-child(2),
                &:nth-child(3),
                &:nth-child(6),
                &:nth-child(7) {
                    display: none;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                padding: 1.2rem 2rem;
                &:nth-child(2),
                &:nth-child(3),
                &:nth-child(6),
                &:nth-child(7) {
                    display: none;
                }
                &:last-child {
                    width: 10rem;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                padding: 1.2rem 2rem;
                &:nth-child(2),
                &:nth-child(3),
                &:nth-child(6) {
                    display: none;
                }
                &:last-child {
                    width: 10rem;
                }
            }
            @include susy-breakpoint($s-1201, $c18) {
                padding: 1.2rem 2rem;
                &:last-child {
                    width: 10rem;
                }
            }
            @include susy-breakpoint($s-1601, $c24) {
                padding: 1.2rem 2rem;
            }
        }
        td {
            text-align: center;
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
            vertical-align: middle;
            padding: 1.8rem 4rem;
            &:nth-of-type(1) {
                text-align: left;
                max-width: 20rem;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
            }
            &:last-child {
                position: relative;
                text-align: center;
                a {
                    display: flex;
                    position: relative;
                    align-items: center;
                    justify-content: center;
                    > span {
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate3d(140%,-50%,0);
                        font-size: var(--font-size-14);
                        color: var(--grey-color-52);
                        width: 2rem;
                        height: 2rem;
                    }
                }
            }
            a {
                color: var(--grey-color-52);
                transition: all 300ms ease;
                &:hover {
                    color: var(--main-color-500);
                }
                img {
                    width: 2.6rem;
                    display: inline-block;
                    vertical-align: middle;
                    @include susy-breakpoint($s-768, $c12) {
                        width: 2rem;
                    }
                }
            }
            > img {
                width: 2.6rem;
                display: inline-block;
                vertical-align: middle;
                @include susy-breakpoint($s-768, $c12) {
                    width: 2rem;
                }
            }
            @include susy-breakpoint($s-320, $c8) {
                padding: 1rem 1.2rem;
                &:nth-child(4),
                &:nth-child(5),
                &:nth-child(8),
                &:nth-child(9) {
                    display: none;
                }
                &:nth-child(3) {
                    max-width: 10rem;
                }
            }
            @include susy-breakpoint($s-768, $c12) {
                padding: 1.2rem 2rem;
                &:nth-child(4),
                &:nth-child(5),
                &:nth-child(8),
                &:nth-child(9) {
                    display: none;
                }
                &:last-child {
                    width: 10rem;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                padding: 1.2rem 2rem;
                &:nth-child(4),
                &:nth-child(5),
                &:nth-child(8) {
                    display: none;
                }
                &:nth-child(1) {
                    max-width: 15rem;
                }
            }
            @include susy-breakpoint($s-1201, $c18) {
                padding: 1.2rem 2rem;
                &:nth-child(1) {
                    max-width: 15rem;
                }
            }
            @include susy-breakpoint($s-1601, $c24) {
                padding: 1.2rem 2rem;
            }
        }
    }
    &__post-wrap {
        display: grid;
        grid-template-columns: 1fr 28rem;
        border-radius: 1rem;
        overflow: hidden;
        height: 100%;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: 1fr;
        }
        @include susy-breakpoint($s-960, $c12) {
            grid-template-columns: 1fr 24rem;
        }
    }
    &__post-content {
        box-sizing: border-box;
        background-color: var(--grey-color-eb);
        padding: 5rem;
        @include susy-breakpoint($s-320, $c8) {
            background-color: #fff;
            padding: 2rem 1.2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            background-color: #fff;
            padding: 0.8rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding: 2rem;
        }
    }
    &__post-info {
        box-sizing: border-box;
        position: relative;
        background-color: var(--grey-color-57);
        padding: 11.7rem 4rem 0;
        @include susy-breakpoint($s-320, $c8) {
            padding: 4rem 2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            padding: 4rem 2rem;
        }
    }
    &__btn-back {
        position: absolute;
        right: 3rem;
        top: 3rem;
        z-index: 10;
        i {
            font-size: var(--font-size-22);
            color: #fff;
        }
        @include susy-breakpoint($s-320, $c8) {
            right: 2rem;
            top: 2.6rem;
            i {
                color: var(--grey-color-48);
            }
        }
        @include susy-breakpoint($s-768, $c12) {
            right: 1.2rem;
            top: 1.4rem;
            i {
                color: var(--grey-color-48);
            }
        }
    }
    &__post-header {
        margin-bottom: 3rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 1.6rem;
        }
        h2 {
            font-size: var(--font-size-24);
            line-height: 1.4;
            color: var(--grey-color-52);
            font-weight: 500;
            @include susy-breakpoint($s-320, $c8) {
                font-size: var(--font-size-20);
                padding-right: 3.6rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                padding-right: 3.6rem;
            }
        }
    }
    &__post-article {
        padding-bottom: 3rem;
        margin-bottom: 3rem;
        border-bottom: 0.1rem solid var(--grey-color-48);
        P {
            font-size: var(--font-size-16);
            color: var(--grey-color-52);
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 3rem;
            }
        }
        > div {
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
            &:not(:last-child) {
                margin-bottom: 3rem;
            }
        }
        textarea {
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
            border: 0;
            width: 100%;
            padding: 0;
            background-color: transparent;
            resize: none;
            overflow-x: hidden;
            overflow-y: scroll;
            min-height: 48rem;
            -webkit-overflow-scrolling: touch;
        }
    }
    &__rows {
        display: flex;
        gap: 2rem;
        &:not(:last-child) {
            margin-bottom: 3rem;
        }
        span {
            font-size: var(--font-size-14);
            color: var(--grey-color-ba);
            &:nth-child(1) {
                flex-basis: 6rem;
            }
        }
    }
    &__attachment {
        @include susy-breakpoint($s-320, $c8) {
            display: flex;
            gap: 1rem;
        }
    }
    &__btn-file {
        display: flex;
        gap: 2rem;
        align-items: center;
        &:not(:last-child) {
            margin-bottom: 2rem;
        }
        @include susy-breakpoint($s-320, $c8) {
            &:not(:last-child) {
                margin-bottom: 0;
            }
        }
        img {
            width: 3rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 4.5rem;
            }
        }
        span {
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
            line-height: 1.6;
            @include susy-breakpoint($s-320, $c8) {
                display: none;
            }
        }
    }
    &__empty {
        min-height: 48rem;
        display: flex;
        justify-content: center;
        align-items: center;
        @include susy-breakpoint($s-320, $c8) {
            min-height: 40rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            min-height: 40rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            min-height: 40rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            min-height: 40rem;
        }
        img {
            width: 31.5rem;
            display: block;
            margin: 0 auto 1.6rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 28rem;
            }
            @include susy-breakpoint($s-768, $c12) {
                width: 28rem;
            }
        }
        p {
            text-align: center;
            font-size: var(--font-size-14);
            color: var(--grey-color-52);
        }
    }
}

.history-board-search {
    background-color: var(--grey-color-dd);
    border-radius: 0.4rem;
    padding: 2.4rem 4.4rem;
    margin-bottom: 3.2rem;
    @include susy-breakpoint($s-320, $c8) {
        background-color: var(--grey-color-dd);
        margin: 1.6rem 1.6rem 2.4rem;
        border-radius: 0.8rem;
        padding: 1.6rem;
    }
    @include susy-breakpoint($s-768, $c12) {
        padding: 1.2rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        padding: 2.4rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        padding: 2.4rem 3.2rem;
    }
    & > h3 {
        font-size: var(--font-size-20);
        color: var(--grey-color-52);
        font-weight: normal;
        margin-bottom: 0.8rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 1.6rem;
        }
    }
    &__radio-btn-wrap {
        display: flex;
        gap: 0.8rem;
        @include susy-breakpoint($s-320, $c8) {
            gap: 1.6rem;
        }
    }
    &__select-wrap {
        @extend %site-select;
        &::after {
            background-color: var(--grey-color-a2);
        }
        @include susy-breakpoint($s-320, $c8) {
            &::after {
                width: 7.4rem;
            }
            &::before {
                right: 2.6rem;
            }
            select {
                padding-right: 7.4rem;
                background-color: #fff;
            }
        }
    }
    &__form {
        display: grid;
        grid-template-columns: repeat(3, 1fr) 14.7rem;
        gap: 5.6rem;
        @include susy-breakpoint($s-320, $c8) {
            grid-template-columns: 1fr;
            gap: 1.2rem;
        }
        @include susy-breakpoint($s-768, $c12) {
            grid-template-columns: repeat(2, 1fr);
            gap: 1.2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            grid-template-columns: repeat(2, 1fr);
            gap: 1.6rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            gap: 2.4rem;
        }
    }
    &__row {
        &:not(:last-child) {
            margin-bottom: 0.8rem;
            @include susy-breakpoint($s-320, $c8) {
                margin-bottom: 1.2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                margin-bottom: 1.6rem;
            }
        }
        input[type="text"] {
            width: 100%;
            box-sizing: border-box;
            background-color: #fff;
            border: none;
            border-radius: 0.8rem;
            padding: 1rem;
            color: var(--grey-color-52);
            font-size: var(--font-size-14);
            &::placeholder {
                color: var(--grey-color-69);
            }
            @include susy-breakpoint($s-768, $c12) {
                height: 4.8rem;
            }
        }
    }
    &__radio-wrap {
        background-color: #fff;
        border-radius: 0.8rem;
        padding: 1rem;
        & > p {
            font-size: var(--font-size-14);
            color: var(--grey-color-69);
            margin-bottom: 0.8rem;
        }
    }
    &__radio-btn-wrap {
        display: flex;
        justify-content: flex-start;
        @include susy-breakpoint($s-768, $c12) {
            gap: 0.4rem;
        }
    }
    &__btn-submit {
        background-color: var(--main-color-300);
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0;
        color: #fff;
        font-size: var(--font-size-14);
        border-radius: 0.8rem;
        height: 100%;
        transition: all 300ms ease;
        &:hover {
            background-color: var(--main-color-400);
        }
        &:active {
            background-color: var(--main-color-400);
        }
        @include susy-breakpoint($s-320, $c8) {
            height: 4.8rem;
            width: 18rem;
            margin: 0 auto;
            border-radius: 4.8rem;
        }
    }
    &__content {
        @include susy-breakpoint($s-320, $c8) {
            &:last-child {
                padding: 0.8rem;
            }
        }
    }
}
